import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Helmet from "react-helmet";
import { formatDolar } from "./TablePlan";
import axios from "axios";
import { apiURL } from "../modules/Enums";
import Cookies from "universal-cookie";

function refreshPage() {
  window.location.reload(false);
}

export default function Sidebar() {

  const cookies = new Cookies();

  const [User, setUser] = useState({
    fullname: "coinproy",
    saldo: {
      holdeo: {
        invest: 0,
        retreat: 0
      }
    }
  })

  useEffect(() => {

    axios.get(apiURL + "/users/user/" + cookies.get("id", { path: "/dashboard" }))
      .then(res => {
        setUser(res.data);
        return;
      });
  }, [])

  function handleSignOut() {

    if (cookies.get("id")) {
      cookies.remove("id", { path: "/" });
      cookies.remove("email", { path: "/" });
      sessionStorage.clear();
      localStorage.clear();
      window.location.href = "../login"
    }
  }

  return (
    <React.Fragment>
      <nav className="sidebar sidebar-offcanvas" id="sidebar">
        <div className="sidebar-brand-wrapper d-none d-lg-flex align-items-center justify-content-center fixed-top" onClick={refreshPage}>
          <Link className="sidebar-brand brand-logo" to="/dashboard" >
            <img src="assets/images/logo.svg" alt="logo" />
          </Link>
          <Link className="sidebar-brand brand-logo-mini" to="/dashboard">
            <img src="assets/images/logo_mini.png" alt="logo" />
          </Link>
        </div>
        <ul className="nav">
          <li className="nav-item profile">
            <div className="profile-desc">
              <div className="profile-pic">
                <div className="count-indicator">
                  <img
                    className="img-xs rounded-circle "
                    src="assets/images/faces-clipart/pic-4.png"
                    alt=""
                  />
                  {/* <span class="count bg-success"></span> */}
                </div>
                <div className="profile-name">
                  <h5 className="mb-0 font-weight-normal">{User.fullname}</h5>
                  <span>
                    Saldo Inversión <strong>{formatDolar(User.saldo.holdeo.invest)}$</strong>
                  </span>
                  <br />
                  <span>
                    Saldo Retiro <strong>{formatDolar(User.saldo.holdeo.retreat)}$</strong>
                  </span>
                </div>
              </div>
              <a href="#0" id="profile-dropdown" data-toggle="dropdown">
                <i className="mdi mdi-dots-vertical" />
              </a>
              <div
                className="dropdown-menu dropdown-menu-right sidebar-dropdown preview-list"
                aria-labelledby="profile-dropdown"
              >
                <Link to="/myaccount" className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-settings text-primary" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1 text-small">
                      Ajustes
                    </p>
                  </div>
                </Link>
                <div className="dropdown-divider" />
                <a class="dropdown-item preview-item" onClick={() => handleSignOut()}>
                  <div class="preview-thumbnail">
                    <div class="preview-icon bg-dark rounded-circle">
                      <i class="mdi mdi-logout text-danger"></i>
                    </div>
                  </div>
                  <div class="preview-item-content">
                    <p class="preview-subject mb-1">Cerrar Sesión</p>
                  </div>
                </a>
                <div className="dropdown-divider" />
              </div>
            </div>
          </li>
          <li className="nav-item nav-category">
            <span className="nav-link">Menu</span>
          </li>
          <li className="nav-item menu-items" onClick={refreshPage}>
            <Link className="nav-link" to="/dashboard">
              <span className="menu-icon">
                <i className="mdi mdi-speedometer" />
              </span>
              <span className="menu-title">Dashboard</span>
            </Link>
          </li>
          <li className="nav-item menu-items">
            <a
              className="nav-link"
              data-toggle="collapse"
              href="#ui-basic"
              aria-expanded="false"
              aria-controls="ui-basic"
            >
              <span className="menu-icon">
                <i className="mdi mdi-laptop" />
              </span>
              <span className="menu-title">Planes</span>
              <i className="menu-arrow" />
            </a>
            <div className="collapse" id="ui-basic">
              <ul className="nav flex-column sub-menu">
                <li className="nav-item">
                  {" "}
                  <Link className="nav-link" to="/newplan">
                    Crear Plan
                  </Link>
                </li>
                <li className="nav-item" >
                  {" "}
                  <Link className="nav-link" to="/myplans">
                    Mis Planes
                  </Link>
                </li>
                <li className="nav-item" onClick={refreshPage}>
                  {" "}
                  <Link className="nav-link" to="/history">
                    Historial
                  </Link>
                </li>
              </ul>
            </div>
          </li>
          <li className="nav-item menu-items" onClick={refreshPage}>
            <Link className="nav-link" to="/invest">
              <span className="menu-icon">
                <i className="mdi mdi-playlist-play" />
              </span>
              <span className="menu-title">Solicitar Saldo</span>
            </Link>
          </li>
          <li className="nav-item menu-items" onClick={refreshPage}>
            <Link className="nav-link" to="/market-coins">
              <span className="menu-icon">
                <i className="mdi mdi-playlist-play" />
              </span>
              <span className="menu-title">Market Coin</span>
            </Link>
          </li>
        </ul>
      </nav>
    </React.Fragment>
  );
}
