import React from 'react'
import { Route, Routes } from 'react-router-dom'
import CPanelFormSendMail from './CPanelFormSendMail'

export default function CPanelActionUser() {



    return (
        <>
            <h4>Acciones</h4>
            <div className="col">
                <button className='btn btn-warning' onClick={() => window.location.href += "/sendmail"}><strong>Enviar correo electronico</strong></button>
            </div>
            <div className="col">
                <button className='btn btn-success'>Recargar saldo</button>
            </div>
            <div className="col">
                <button className='btn btn-primary'>Editar</button>
            </div>
        </>
    )
}
