import axios from 'axios';
import React from 'react'
import { apiURL, idTags } from '../modules/Enums'
import { formatDolar } from './TablePlan';

import Cookies from 'universal-cookie'
import { changeDateFormat } from '../modules/CPDate';
import toast, { Toaster } from 'react-hot-toast';

export function sumTotalUtility() {
    const sumTotal = Number.parseFloat(sessionStorage.getItem(idTags.tbUtilNormal))
        + Number.parseFloat(sessionStorage.getItem(idTags.tbUtilIntermedio))
        + Number.parseFloat(sessionStorage.getItem(idTags.tbUtilAdvance));

    return sumTotal;
}

export function calcWithPorcentToDolar(porcent, invest) {
    const dolar = porcent / 100 * invest;
    return dolar;
}

export default function FormClosePlan(props) {



    function handleAceptClosePlan(e) {
        e.preventDefault();

        document.querySelector("#formClosePlan").classList.add("d-none");
        document.querySelector("#formConfirm").classList.remove("d-none");

    }

    function calcPorcentToDolar(porcent) {
        const dolar = porcent / 100 * sumTotalUtility();
        return dolar;
    }


    function handleCofirm(e) {
        e.preventDefault();
        const inpPassword = document.querySelector("#floatingPassword").value;

        const cookies = new Cookies();

        axios.get(apiURL + "/users/user/" + cookies.get("id"))
            .then(res => {
                const user = res.data;
                if (user.password !== inpPassword) {
                    alert("CONTRASEÑA INCORRECTA");
                    return;
                }


                /* TERMINAR */
                const subtotal = sumTotalUtility();
                let porcentDolar = calcPorcentToDolar(props.PorcentDiscount);
                let total = subtotal - porcentDolar;
                const retreat = total - calcWithPorcentToDolar(20, total);
                const reinvest = total - calcWithPorcentToDolar(80, total);

                user.saldo.holdeo.retreat += retreat;
                user.saldo.holdeo.invest += reinvest;

                const planId = sessionStorage.getItem("planClickedId");
                axios.put(apiURL + "/users/user/" + cookies.get("id"), user)
                    .then(async res => {
                        if (res.status == 200) {
                            // const currentDate = await getCurrentDateServer();
                            const resCurrentDate = await axios.get(apiURL + "/date/current")
                            const currentDate = resCurrentDate.data;

                            const data = {
                                dateEmision: changeDateFormat(props.planClicked.date),
                                dateFinally: currentDate.current,
                                name: props.planClicked.name,
                                retreat: retreat,
                                rangeSelected: props.planClicked.rangeSelected,
                                case: sessionStorage.getItem("case") == "2" ? "Cerrado" : "Finalizado",
                                invest: props.planClicked.invest,
                                coins: {
                                    normal: props.planClicked.coinsNormal,
                                    intermedio: props.planClicked.coinsIntermedio,
                                    advance: props.planClicked.coinsAdvance
                                }
                            }

                            // CREATE HISTORY
                            await axios.post(apiURL + "/users/user/" + cookies.get("id") + "/plans/" + "history/holdeo", data)
                            // DELETE PLAN
                            const resPromise = axios.delete(apiURL + "/users/user/" + cookies.get("id") + "/plan/" + planId);
                            toast.promise(resPromise, {
                                loading: "Cerrando Plan...",
                                success: "Plan Cerrado su dinero se encuentra en saldo retiro. listo para retirar a su cuenta bancaria o en USDT",
                                error: error => "Error al cerrar plan. ERROR: " + error.message
                            }, { duration: 3500 })
                                .then(res => {
                                    // alert("Proceso completo, su dinero se encuentra en saldo retiro. listo para retirar a su cuenta bancaria o en USDT")
                                    sessionStorage.removeItem("case");
                                    setTimeout(() => {
                                        window.location.href = "./myplans";
                                    }, 3600);
                                })
                        }
                    })


            })
    }

    return (
        <>
            <Toaster />
            <div className="container">
                <form id="formClosePlan" className='d-none'>
                    <h5>Retirar: <strong style={{ color: 'green' }}>{formatDolar(sumTotalUtility() - calcPorcentToDolar(props.PorcentDiscount))}$</strong></h5>
                    <span><strong>Leer.</strong></span><br />
                    <span>
                        Se realizara un envio a su saldo de retiro del 80% de su plan holdeado mas su inversion, Disponible para retirar a su cuenta <strong>USDT</strong>.
                        el 20% restante sera reinvertido automaticamente en la plataforma.
                    </span><br /><br />
                    <button className='btn btn-success' onClick={(e) => handleAceptClosePlan(e)}>Desea Aceptar</button>
                    <br /><br />



                </form>

                <form id="formConfirm" className='d-none'>
                    <h5>Confirmar</h5>
                    <div className="form-floating">
                        <input type="password" className="form-control" id="floatingPassword" placeholder="Password" style={{ width: '400px' }} />
                        <label htmlFor="floatingEmail">Introduzca su Contraseña</label><br />
                        <button className='btn btn-success' onClick={(e) => handleCofirm(e)}>Aceptar</button>
                    </div>
                </form>
            </div>
        </>
    )
}
