import React, { Component } from 'react'

import axios from 'axios'
import { apiURL } from '../modules/Enums';
import Cookies from 'universal-cookie';
import { formatDolar } from './TablePlan';

export default class PrincipalDataUser extends Component {

    state = {}

    async componentDidMount() {
        try {
            const res = await axios.get(apiURL + "/users/user/" + new Cookies().get("id"));
            const user = res.data;

            const { email } = user;
            const saldo = user.saldo === undefined ? 0 : user.saldo.holdeo.invest;
            const saldoRetreat = user.saldo === undefined ? 0 : user.saldo.holdeo.retreat;

            this.setState({ email: email, saldoinvest: saldo, saldoRetreat: saldoRetreat });
        } 
        catch (error) {console.log(error)}

    }

    render() {
        return (
            <div className="mb-5 d-flex" >
                <div className="col">
                    <h5>{this.state.email}</h5>
                </div>
                <div className="col-md-4 text-center">
                    <h5>Saldo: <strong style={{ color: 'green' }}>{formatDolar(this.state.saldoinvest)}$</strong></h5>
                </div>
                <div className="col-md-4 text-center">
                    <h5>Saldo Retiro: <strong style={{ color: 'green' }}>{formatDolar(this.state.saldoRetreat)}$</strong></h5>
                </div>
            </div>
        )
    }
}