import React from 'react'

export default function TableUtility({idTag, currentPrices, coins, porcent, invest}) {
   
    function handleClickUtil() {
        const table = document.querySelector("#"+idTag);

        table.classList.toggle("d-none");
    }

    function calcPorcentToDolar(rangePorcent, sizeCoins) {
        let calc = (rangePorcent / 100 * invest) / sizeCoins;
        return formatDolar(calc);
    }

    function calcPorcentToCrypto(rangePorcent, cryptoPrice, sizeCoins) {
        const calcDolar = calcPorcentToDolar(rangePorcent, sizeCoins);
        const calcCrypto = calcDolar / cryptoPrice;
        return formatCrypto(calcCrypto);
    }

    function formatCrypto(crypto) {
        crypto = crypto + "";
        return crypto.substr(0, 10);
    }

    function formatDolar(dolar) {
        let strDolar = dolar + "";
        let indexPoint = strDolar.indexOf(".");
        return strDolar.substr(0, indexPoint == -1 ? 6 : indexPoint + 3)
    }

    function getAmountTotal () {
        let total = 0;
        
        for (let idx = 0; idx < currentPrices.length; idx++) {
            const currentPricesCoins = currentPrices[idx].current_price;
            const crypto = calcPorcentToCrypto(porcent, coins[idx].price, coins.length)
            const fact = formatDolar(crypto * currentPricesCoins);
            total += Number.parseFloat(fact);
        }

        sessionStorage.setItem(idTag, formatDolar(total));
        
        return formatDolar(total);
    }

    function utilityDolar () {
        const investRange =  calcPorcentToDolar(porcent, coins.length) * coins.length;
        return formatDolar(getAmountTotal() - investRange);
    }

    function getUtilityPorcent () {        
        const currentAmount = getAmountTotal();
        const investRange = calcPorcentToDolar(porcent, coins.length) * coins.length;
        return formatDolar((currentAmount / investRange * 100) - 100);
    }
    
    return (
        <div className="container">
            <button className='btn btn-success' onClick={() => handleClickUtil()}>Utilidad</button>

            <table id={idTag} className='table mt-3 d-none'>
                <thead>
                    <tr>
                        <td scope='col'><strong>Monto Total</strong></td>
                        <td scope='col'><strong>Inversion rango</strong></td>
                        <td scope='col'><strong>Utilidad Dolar</strong></td>
                        <td scope='col'><strong>Utilidad %</strong></td>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td><strong style={{color: 'green'}}>{getAmountTotal()}$</strong></td>
                        <td><strong style={{color: 'green'}}>{calcPorcentToDolar(porcent, coins.length) * coins.length}$</strong></td>
                        <td>
                            {
                                Number.parseFloat(utilityDolar()) < 0 ?
                                <strong style={{color: 'red'}}>{utilityDolar()}$</strong>
                                :
                                <strong style={{color: 'green'}}>{utilityDolar()}$</strong>
                            }
                        </td>
                        <td><strong style={{color: 'green'}}>{getUtilityPorcent()}%</strong></td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}
