import React, { Component } from 'react'
import { Link, } from 'react-router-dom';
import { reedir, routes } from '../modules/JsRoute';
import axios from 'axios';

import SubHeader from '../components/SubHeader';
import '../css/dashboard.css'
import PrincipalDataUser from '../components/PrincipalDataUser';
import { apiURL } from '../modules/Enums';
import Cookies from 'universal-cookie';

export default class MyInvestment extends Component {

    state = {};

    async componentDidMount() {
        const cookies = new Cookies;
        if (!cookies.get("id")) window.location.href = "./login"

        const res = await axios.get(apiURL + "/users/user/" + JSON.parse(sessionStorage.getItem("userVerify")));
        const user = res.data;

        this.setState({ user: user.email });
    }


    render() {
        return (
            <>
                <SubHeader />
                <div className="container-fluid">
                    <div className="row">
                        <nav id="sidebarMenu" className="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse sb-dash" style={{ height: "790px" }}>
                            <div className="position-sticky pt-3">
                                <ul className="nav flex-column">
                                    <li className="nav-item">
                                        <Link to="newplan" className="nav-link list-group-item list-group-item-action" aria-current="page" href="/dashboard/myaccount">
                                            {/* <img src="" width={25} height={25} /> */}
                                            Crear Nuevo Plan
                                        </Link>
                                    </li>
                                    <li className="nav-item mt-2">
                                        <Link to="myplans" className="nav-link list-group-item list-group-item-action">
                                            Mis Planes
                                        </Link>
                                    </li>
                                    {/* <li className="nav-item mt-2">
                                        <a className="nav-link list-group-item list-group-item-action" href="./dolar_shop.php">
                                            <img src="" width={25} height={25} />
                                            Mis Planes
                                        </a>
                                    </li> */}
                                    <li className="nav-item mt-2">
                                        <Link to="history" className="nav-link list-group-item list-group-item-action">
                                            Historial
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                        <main className="col-md-9 ms-sm-auto col-lg-10 px-md-4"><div className="chartjs-size-monitor"><div className="chartjs-size-monitor-expand"><div className /></div><div className="chartjs-size-monitor-shrink"><div className /></div></div>
                            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                                <h3>Inversiones CryptoHMR</h3>
                            </div>

                            <div className="container">
                                <PrincipalDataUser />
                            </div>

                            <div className="row mb-2">
                                <div className="col-md-6">
                                    <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
                                        <div className="col p-4 d-flex flex-column position-static">
                                            <strong className="d-inline-block mb-2 text-primary">Inversiones Crypto</strong>
                                            <h3 className="mb-0">Gestion de Planes</h3>
                                            <div className="mb-1 text-muted">Beneficios</div>
                                            <p className="card-text mb-auto">Gestiona tus planes, genera beneficios cada tres meses con la mejor estrategia que desees</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </main>
                    </div>
                </div>
            </>

        )
    }
}
