import React, { useEffect, useState } from 'react'
import Helmet from 'react-helmet'

import { getCoins } from '../../modules/Coins'


export default function CPanelGeneral() {
    const TITLE = "cPanel - Cuentas";

    const [coins, setCoins] = useState([]);
    
    useEffect(() => {

        getCoins()
            .then(res => {
                setCoins(res.coinsFilter);
            })
        
    }, [])

    return (
        <>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>

            <div className="container">
                <div className="row">
                    <div className="col">
                        <button className='btn btn-warning'>Falta comprar</button>
                    </div>
                </div>

                <div className="mt-5">
                    <h4>Comprado</h4>
                </div>
                <table className='table table-hover table-striped'>
                    <thead>
                        <tr>
                            {
                                coins.map(coin => {
                                    return <th scope='col'>{coin.symbol}</th>
                                })
                            }
                        </tr>
                    </thead>

                    <tbody>
                        
                    </tbody>
                    
                </table>
            </div>
        </>
    )
}
