import React, { Component, useState } from 'react'

import { Link } from 'react-router-dom';
import axios from 'axios';
import { getCoins } from '../modules/Coins';
import { apiURL } from '../modules/Enums';
import Cookies from 'universal-cookie';
import toast, { Toaster } from 'react-hot-toast';
import Helmet from 'react-helmet';

import Navbar from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import Footer from '../components/Footer';


export default class NewPlanInvestment extends Component {

    TITLE = "New Plan - Holdeo"

    state = { boxs_checked: 2, lv_normal: 10, lv_intermedio: 10, lv_advance: 10, sum_total_porcent: 0, tableCoins: { normal: [{ name: "Bitcoin" }, { name: "Ethereum" }], intermedio: [], advance: [] } };
    coins = []
    coins_ref = [];
    boxs = []
    inp_ranges = {};


    cookies = new Cookies();

    styles = {
        bgIntermedio: "#404f1f",
        bgAdvance: "#1f4f39"
    }

    async componentDidMount() {

        if (!this.cookies.get("id")) window.location.href = "../login";

        // AXIOS DB
        const res = await axios.get(apiURL + "/users/user/" + this.cookies.get("id"));
        const user = res.data;

        const filterCoins = [];

        const _res = await getCoins();
        this.coins = _res.coinsFilter;

        // FILTRAR MONEDAS LAS 20 PRIMERAS
        for (let i = 2; i < _res.coinsFilter.length; i++) {
            const coin = _res.coinsFilter[i];
            if (i < 21) {
                filterCoins.push(coin);
            }
        }

        this.coins = filterCoins;

        this.setState({
            user: user.email,
            fullname: user.fullname,
            saldo: user.saldo.holdeo.invest,
            price_btc: _res.coinsFilter[0].current_price,
            price_eth: _res.coinsFilter[1].current_price,
            current_range: "Intermedio"
        });
        this.coins_ref = filterCoins;

        this.inp_ranges.lv_normal = document.querySelector("#lv_normal");
        this.inp_ranges.lv_intermedio = document.querySelector("#lv_intermedio");
        this.inp_ranges.lv_advance = document.querySelector("#lv_advance");
    }

    // handle_clickLevel(e) {
    //     const range_selected = e.target.innerText;
    //     // const max_coins = range_selected.toLowerCase() === "todos los rangos" ? 10 : 5;
    //     const ranges = range_selected.toLowerCase() === "todos los rangos" ? 2 : 1;

    //     const max_coins = ranges === 2 ? 10 : 5;
    //     let filter_coins = [];

    //     for (let i = 0; i < this.coins_ref.length; i++) {
    //         const coin = this.coins_ref[i];

    //         if (i < (max_coins * 2) - 2) {
    //             filter_coins.push(coin);
    //         }
    //     }

    //     this.coins = filter_coins;
    //     this.setState({ range_selected: range_selected, ranges: ranges, max_coins: max_coins, current_range: "Intermedio" });
    //     console.log(this.state);
    // }

    /* CUANDO SE SELECCIONA UN BOX */
    handle_checked(e) {

        const boxs = document.querySelectorAll("input.form-check-input")
        let arr_boxs = Array.from(boxs);

        e.target.checked = true;
        e.target.disabled = true;

        let boxs_checked = arr_boxs.filter(box => box.checked);
        let coins_selected = boxs_checked.length;

        if (coins_selected === 5) {
            this.setState({ intermedio: boxs_checked, current_range: "Avanzado" });
            arr_boxs = arr_boxs.filter(box => !box.checked);
            boxs_checked.forEach((box, idx) => {
                if (idx > 1) {
                    box.parentElement.parentElement.className += ' bg-intermedio';
                    box.parentElement.parentElement.style.background = this.styles.bgIntermedio;
                }
            })
            boxs_checked = [];

        }

        if (boxs_checked.length === 10) {
            const list_coins = document.querySelector("#list_coins");
            const list = list_coins.children;

            let list_boxs = [];

            for (let i = 2; i < list.length; i++) {
                const element = list[i];
                if (!element.classList.contains("bg-intermedio")) {
                    list_boxs.push(element);
                }
            }

            list_boxs = list_boxs.filter(li => li.firstChild.firstChild.checked);
            list_boxs.forEach(li => {
                li.classList.add("bg-advance");
                li.style.background = this.styles.bgAdvance;
            });
            list_boxs[list_boxs.length - 1].firstChild.firstChild.checked = true;

            boxs_checked = list_boxs.filter(li => li.firstChild.firstChild.checked);
            this.setState({ advance: boxs_checked });
        }

        if (this.state.coins_selected >= 10) {
            toast.error("Ya has seleccionado un total de 10 monedas", { duration: 2000 });
            if (!e.target.checked) e.target.checked = true;
            if (e.target.checked) {
                e.target.checked = false;
                coins_selected -= 1;
                this.setState({ coins_selected: coins_selected });
            }
        }

        console.log(e.target.checked)
        const coinSelectedName = e.target.parentElement.children[1].innerText;

        if (coins_selected >= 3 && coins_selected <= 5) {
            this.state.tableCoins.intermedio.push({ name: coinSelectedName });
        }
        if (coins_selected > 5 && e.target.checked) {
            this.state.tableCoins.advance.push({ name: coinSelectedName });
        }
        // this.state.tableCoins.intermedio =  ? 

        this.setState({ coins_selected: coins_selected });
        console.log(this.state);
    }

    handle_clear(e) {
        e.preventDefault();

        const boxs = document.querySelectorAll("input.form-check-input")
        let arr_boxs = Array.from(boxs);

        arr_boxs.filter((box, idx) => {
            if (idx > 1) {
                if (box.checked) {
                    box.checked = false;
                    const li = box.parentElement.parentElement;
                    li.classList.remove(li.classList.contains("bg-intermedio") ? "bg-intermedio" : "bg-advance");
                    li.style.background = "#1d1d1d";
                    li.children[0].firstChild.disabled = false;
                }
            }

        });
        this.setState({ coins_selected: 0, tableCoins: { normal: [{ name: "Bitcoin" }, { name: "Ethereum" }], intermedio: [], advance: [] } })
    }

    handle_range(e) {
        this.setState({
            lv_normal: e.target.id === "lv_normal" ? e.target.value : this.state.lv_normal,
            lv_intermedio: e.target.id === "lv_intermedio" ? e.target.value : this.state.lv_intermedio,
            lv_advance: e.target.id === "lv_advance" ? e.target.value : this.state.lv_advance,
        })

        let porcent_normal = Number.parseInt(document.querySelector("#lv_normal").value);
        let porcent_intermedio = Number.parseInt(document.querySelector("#lv_intermedio").value);
        let porcent_advance = Number.parseInt(document.querySelector("#lv_advance").value)

        const inversion = Number.parseInt(document.querySelector("#exampleInputInvest").value);

        const calcPorcentDolar_normal = (inversion / 100) * porcent_normal;
        const calcPorcentDolar_intermedio = (inversion / 100) * porcent_intermedio;
        const calcPorcentDolar_advance = (inversion / 100) * porcent_advance;

        const sumTotalPorcent = porcent_normal + porcent_intermedio + porcent_advance;
        const lb_sumTotalPorcent = document.querySelector("#sumTotalPorcent");

        this.setState({
            calc_porcent_normal: calcPorcentDolar_normal,
            calc_porcent_intermedio: calcPorcentDolar_intermedio,
            calc_porcent_advance: calcPorcentDolar_advance,
            sum_total_porcent: sumTotalPorcent
        })

        if (sumTotalPorcent !== 100) lb_sumTotalPorcent.style.background = "red";
        if (sumTotalPorcent === 100) lb_sumTotalPorcent.style.background = "lime";

        localStorage.setItem("porcents", JSON.stringify({
            porcent_normal: porcent_normal,
            porcent_intermedio: porcent_intermedio,
            porcent_advance: porcent_advance
        }));
    }

    // handle_rangeMax() {
    //     const lv_normal = this.state.lv_normal;
    //     const lv_intermedio = this.state.lv_intermedio;
    //     const lv_advance = this.state.lv_advance;

    //     // lv_normal 
    // }

    async handle_createPlan(e) {
        e.preventDefault();

        const inps_intermedio = this.state.intermedio;
        const inps_advance = this.state.advance;

        // ELIMINAR DEL ARRAY BTC Y ETH
        inps_intermedio.splice(0, 1);
        inps_intermedio.splice(0, 1);

        let coinsAdvance = [];
        let coinsIntermedio = [];
        let coinsNormal = [{ name: "Bitcoin", price: this.state.price_btc }, { name: "Etherium", price: this.state.price_eth }];
        inps_advance.forEach(inp => coinsAdvance.push({ name: inp.childNodes[0].childNodes[1].innerText, price: inp.childNodes[1].childNodes[0].innerText }));
        inps_intermedio.forEach(inp => coinsIntermedio.push({ name: inp.nextSibling.innerText, price: inp.parentElement.nextSibling.childNodes[0].innerText }));

        const coinsIntermedioCopy = [];
        coinsIntermedio.forEach(coin => {
            coinsIntermedioCopy.push({
                name: coin.name,
                price: coin.price.substr(0, coin.price.length - 1).trim()
            });
        });

        const coinsAdvanceCopy = [];
        coinsAdvance.forEach(coin => {
            coinsAdvanceCopy.push({
                name: coin.name,
                price: coin.price.substr(0, coin.price.length - 1).trim()
            });
        });


        coinsIntermedio = coinsIntermedioCopy;
        coinsAdvance = coinsAdvanceCopy;

        const porcent_normal = JSON.parse(localStorage.getItem("porcents")).porcent_normal;
        const porcent_intermedio = JSON.parse(localStorage.getItem("porcents")).porcent_intermedio;
        const porcent_advance = JSON.parse(localStorage.getItem("porcents")).porcent_advance;

        const plan = {
            invest: document.querySelector("#exampleInputInvest").value,
            name: document.querySelector("#exampleInputNamePlan").value,
            rangeSelected: "2",
            coins: { normal: coinsNormal, intermedio: coinsIntermedio, advance: coinsAdvance },
            porcents: { normal: porcent_normal, intermedio: porcent_intermedio, advance: porcent_advance },
            date: null,
            status: null
        };

        // if (plan.invest >= 100) {
        //     const res = await axios.get(apiURL + "/users/user/" + this.cookies.get("id"));
        //     const user = res.data;

        //     if (user.saldo.holdeo.invest >= plan.invest) {

        //         // SOLICITUD DE PLAN
        //         await axios.post(apiURL + `/api/admin/${this.cookies.get("id")}/2525/solicituds/plans`, {
        //             userId: this.cookies.get("id"),
        //             email: this.cookies.get("email"),
        //             invest: plan.invest,
        //             emisionDate: null,
        //             porcents: plan.porcents,
        //             coins: plan.coins
        //         });

        //         console.log("planobj", plan)
        //         // CREATE PLAN
        //         const resPut = axios.post(apiURL + `/users/user/${this.cookies.get("id")}/plans`, plan);

        //         toast.promise(resPut, {
        //             loading: "Creando Nuevo Plan Holdeo...",
        //             success: "Su plan se ha creado correctamente, en breve momentos se le activara su plan Holdeo.",
        //             error: err => "Error al crear plan. ERROR: " + err.message
        //         }, { duration: 5000 }).then(res => {
        //             user.saldo.holdeo.invest -= plan.invest;
        //             axios.put(apiURL + "/users/user/" + this.cookies.get("id"), user).then(resPut => {
        //                 setTimeout(() => {
        //                     window.location.reload();
        //                 }, 5000);
        //             })

        //         })
        //     }
        //     else toast.error("Saldo Insuficiente: su saldo es de " + user.saldo.holdeo.invest + " inversion colocada " + plan.invest, { duration: 5000 });
        // }
        // else toast.error("Minimo no cumplido: su inversion es inferior a 100$. para operar en este plan debe superar o igualar el minimo (100$)",
        //     { duration: 5000 });

        if (plan.invest >= 100) {
            const res = await axios.get(apiURL + "/users/user/" + this.cookies.get("id"));
            const user = res.data;
            if (user.saldo.holdeo.invest >= plan.invest) {
                axios.post(apiURL + `/api/admin/${this.cookies.get("id")}/2525/solicituds/plans`, {
                    userId: this.cookies.get("id"),
                    email: this.cookies.get("email"),
                    invest: plan.invest,
                    emisionDate: null,
                    porcents: plan.porcents,
                    coins: plan.coins
                })

                const resPut = axios.post(apiURL + `/users/user/${this.cookies.get("id")}/plans`, plan);
                user.saldo.holdeo.invest -= plan.invest;

                axios.put(apiURL + "/users/user/" + this.cookies.get("id"), user);
                await toast.promise(resPut, {
                    loading: "Creando su Plan " + plan.name + "...",
                    success: "Su plan se ha creado correctamente, en breve momentos se le activara su plan " + `"${plan.name}"` + ". su planes se encuentran en Mis Planes.",
                    error: (error) => "Su plan no pudo crearse: " + error.message
                }, { duration: 5000 })
            }
            else toast.error("Saldo insuficiente. su saldo es de: " + user.saldo.holdeo.invest + "$");
        }
        else toast.error("Minimo no cumplido: su inversion es inferior a 100$. para operar en este plan debe superar o igualar el minimo (100$)",
            { duration: 5000 });

        // console.log(created);
        // alert("su plan se ha creado correctamente, la activacion se llevara acabo en 24 horas!");

        // if (user.saldo.holdeo >= plan.invest) {
        //     const res = await axios.post(PathRequest.get(`users/user/${JSON.parse(sessionStorage.getItem("userVerify"))}/plans`), plan);
        //     console.log(res);
        //     alert("su plan se ha creado correctamente, la activacion se llevara acabo en 24 horas!");
        // }
        // else {
        //     alert("Saldo Insuficiente");
        // }

        // DBRequest.createPlan(JSON.parse(sessionStorage.getItem("userVerify")), plan, axios);
    }

    render() {
        return (
            <>
                <Toaster />
                <React.Fragment>
                    <Helmet>
                        <title>{this.TITLE}</title>
                    </Helmet>
                    <div className="container-scroller">
                        {/* partial:partials/_sidebar.html */}
                        <Sidebar />
                        {/* partial */}

                        <div className="container-fluid page-body-wrapper">
                            {/* partial:partials/_navbar.html */}
                            <Navbar />
                            {/* partial */}

                            <div className="main-panel">
                                <div className="content-wrapper">
                                    <div className="row">
                                        <div className="col-md-6 grid-margin stretch-card">
                                            <div className="card overlay">
                                                <div className="card-body">
                                                    <h4 className="card-title">Asignación</h4>
                                                    <p className="card-description"> asignar nombre e inversion al plan que ira a crear </p>
                                                    <form className="forms-sample">
                                                        <div className="form-group">
                                                            <label htmlFor="exampleInputNamePlan">
                                                                Nombre del Plan
                                                            </label>
                                                            <input
                                                                type="text"
                                                                min='0'

                                                                className="form-control"
                                                                id="exampleInputNamePlan"
                                                                placeholder="Nombre del Plan"
                                                            />
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="exampleInputInvest">
                                                                Inversion
                                                            </label>
                                                            <input
                                                                type="number"
                                                                min='0'
                                                                max={this.state.saldo}
                                                                className="form-control"
                                                                id="exampleInputInvest"
                                                                placeholder="Inversion"
                                                            />
                                                        </div>

                                                    </form>
                                                    <h4 className="card-title">Preparación</h4>
                                                    <span className='card-description'>
                                                        Asignar el porcentaje de su inversion individualmente a los niveles presente.
                                                    </span>
                                                    <h5 className="card-title text-left mt-3">Nivel Normal</h5>
                                                    <div className="range-slider">
                                                        <input
                                                            className="range-slider__range divy"
                                                            type="range"
                                                            min={10}
                                                            max={100}
                                                            step='5'
                                                            defaultValue={0}
                                                            id='lv_normal'
                                                            onChange={(e) => this.handle_range(e)}
                                                        />
                                                        <span className="range-slider__value" id='lv_normal'>{this.state.lv_normal}%</span>
                                                    </div>
                                                    <h5 className="card-title text-left mt-4">
                                                        Nivel Intermedio
                                                    </h5>
                                                    <div className="range-slider">
                                                        <input
                                                            className="range-slider__range divy"
                                                            type="range"
                                                            min={10}
                                                            max={100}
                                                            step='5'
                                                            defaultValue={0}
                                                            id='lv_intermedio'
                                                            onChange={(e) => this.handle_range(e)}
                                                        />
                                                        <span className="range-slider__value mb-4" id='lv_intermedio'>{this.state.lv_intermedio}%</span>
                                                    </div>
                                                    <h5 className="card-title text-left mt-4">
                                                        Nivel Avanzado
                                                    </h5>
                                                    <div className="range-slider">
                                                        <input
                                                            className="range-slider__range divy"
                                                            type="range"
                                                            min={10}
                                                            max={100}
                                                            step='5'
                                                            defaultValue={0}
                                                            id='lv_advance'
                                                            onChange={(e) => this.handle_range(e)}
                                                        />
                                                        <span className="range-slider__value mb-4" id='lv_advance'>{this.state.lv_advance}%</span>
                                                    </div>
                                                    <h5 className="card-title text-left mt-3 p-2" id='sumTotalPorcent'>
                                                        Porcentaje Total: {this.state.sum_total_porcent}%
                                                    </h5>
                                                    <div className="row ">
                                                        <span className='card-description'>
                                                            Resultado de los niveles de su inversion en Dolares (USDT)
                                                        </span>
                                                        <div className="col-12 grid-margin mt-3">
                                                            <div className="card">
                                                                <div className="">
                                                                    <div className="table-responsive">
                                                                        <table className="table">
                                                                            <thead>
                                                                                <tr>
                                                                                    <th> Nivel </th>
                                                                                    <th> Porcentaje </th>
                                                                                    <th> USD </th>
                                                                                </tr>
                                                                            </thead>
                                                                            <tbody>
                                                                                <tr>
                                                                                    <td>Normal</td>
                                                                                    <td>{this.state.lv_normal}%</td>
                                                                                    <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_normal}$</strong></td>
                                                                                </tr>
                                                                                <tr>
                                                                                    <td>Intermedio</td>
                                                                                    <td>{this.state.lv_intermedio}%</td>
                                                                                    <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_intermedio}$</strong></td>
                                                                                </tr>
                                                                                <tr>
                                                                                    <td>Avanzado</td>
                                                                                    <td>{this.state.lv_advance}%</td>
                                                                                    <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_advance}$</strong></td>
                                                                                </tr>
                                                                            </tbody>
                                                                        </table>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <form>
                                                        {
                                                            this.state.coins_selected == 10 ?
                                                                <div className="row justify-content-center">
                                                                    <button className='btn btn-success' onClick={(e) => this.handle_createPlan(e)}>Crear Plan</button>
                                                                </div>
                                                                :
                                                                <span>Llene los formularios para crear su plan</span>
                                                        }

                                                    </form>

                                                    <div className="border mt-5">
                                                        <h4>PreVisualizador de Niveles</h4>
                                                        <div className="row">
                                                            <div className="col">
                                                                <h5>Normal</h5>
                                                                <ul>
                                                                    {
                                                                        this.state.tableCoins.normal.map(coin => {
                                                                            return (
                                                                                <li>{coin.name}</li>
                                                                            )
                                                                        })
                                                                    }
                                                                </ul>
                                                            </div>
                                                            <div className="col">
                                                                <h5>Intermedio</h5>
                                                                <ul>
                                                                    {
                                                                        this.state.tableCoins.intermedio.map(coin => {
                                                                            return (
                                                                                <li>{coin.name}</li>
                                                                            )
                                                                        })
                                                                    }
                                                                </ul>
                                                            </div>
                                                            <div className="col">
                                                                <h5>Avanzado</h5>
                                                                <ul>
                                                                    {
                                                                        this.state.tableCoins.advance.map(coin => {
                                                                            return (
                                                                                <li>{coin.name}</li>
                                                                            )
                                                                        })
                                                                    }
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>


                                        <div className="col-6 grid-margin">
                                            <div className="card overlay">
                                                <div className="card-body">

                                                    <div className="table-responsive">
                                                        <h4 className="card-title">Seleción de Monedas</h4>
                                                        <h5>Seleccion Maxima {this.state.coins_selected} / 10 {/*this.state.max_coins*/} </h5>
                                                        <h5>Nivel Actual: {this.state.current_range}</h5>

                                                        <ul id="list_coins" className="list-group">
                                                            <li className="list-group-item bg-dark text-white d-flex">
                                                                <div className="col">
                                                                    <input className="form-check-input me-1" type="checkbox" value="bitcoin" checked aria-label="..." />
                                                                    <strong>Bitcoin</strong>
                                                                </div>
                                                                <div className="col">
                                                                    <strong style={{ color: 'lime' }}>{this.state.price_btc}$</strong>
                                                                </div>
                                                            </li>
                                                            <li className="list-group-item bg-dark text-white d-flex">
                                                                <div className="col">
                                                                    <input className="form-check-input me-1" type="checkbox" value="etherium" checked aria-label="..." />
                                                                    <strong>Etherium</strong>
                                                                </div>
                                                                <div className="col">
                                                                    <strong style={{ color: 'lime' }}>{this.state.price_eth}$</strong>
                                                                </div>
                                                            </li>
                                                            {
                                                                this.coins.map(coin => {
                                                                    return (
                                                                        <li className="list-group-item d-flex text-white" style={{ background: "#1d1d1d" }} key={this.state.id}>
                                                                            <div className="col">
                                                                                <input className="form-check-input me-1" type="checkbox" value={coin.id} onClick={(e) => this.handle_checked(e)} aria-label="..." />
                                                                                <strong>{coin.name}</strong> <img src={coin.image} alt="image" width="20" height="20" />
                                                                            </div>
                                                                            <div className="col">
                                                                                <strong style={{ color: 'green' }}>{coin.current_price} $</strong>
                                                                            </div>
                                                                        </li>
                                                                    )
                                                                })
                                                            }
                                                        </ul>

                                                        <br />
                                                        <button className='btn btn-primary' onClick={(e) => this.handle_clear(e)}>Limpiar Seleccion</button>


                                                    </div>
                                                </div>
                                            </div>
                                        </div>


                                    </div>


                                    <div className="row justify-content-center">
                                    </div>{" "}
                                </div>
                                {/* content-wrapper ends */}

                                {/* partial:partials/_footer.html */}
                                <Footer />
                                {/* partial */}
                            </div>
                            {/* main-panel ends */}
                        </div>
                        {/* page-body-wrapper ends */}
                    </div>

                </React.Fragment>

                {/* <div className="container-fluid">
                    <div className="row">
                        <nav id="sidebarMenu" className="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse sb-dash" style={{ height: "790px" }}>
                            <div className="position-sticky pt-3">
                                <ul className="nav flex-column">
                                    <li className="nav-item">
                                        <Link to="/investment/newplan" className="nav-link list-group-item list-group-item-action" aria-current="page" href="/dashboard/myaccount">
                                            
                                            Crear Nuevo Plan
                                        </Link>
                                    </li>
                                    <li className="nav-item mt-2">
                                        < Link to="/investment/myplans" className="nav-link list-group-item list-group-item-action" href="./dolar_shop.php">
                                            
                                            Mis Planes
                                        </ Link>
                                    </li>
                                    
                                    <li className="nav-item mt-2">
                                        <Link to="/investment/history" className="nav-link list-group-item list-group-item-action">
                                            Historial
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                        <main className="col-md-9 ms-sm-auto col-lg-10 px-md-4"><div className="chartjs-size-monitor"><div className="chartjs-size-monitor-expand"><div className /></div><div className="chartjs-size-monitor-shrink"><div className /></div></div>
                            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                                <h3>Crear Nuevo Plan</h3>
                            </div>
                            <div className="container">
                                <form className='mb-5'>
                                    <div className="container col-md-5 border rounded" style={{ height: "35px", width: '550px' }}>
                                        <PrincipalDataUser />
                                    </div>
                                    <div className="list-group">
                                        <div className="list-group-item">
                                            <h3>Asignacion</h3>

                                            <div className="form-floating m-auto"  >
                                                <input type="number" min="50" max={this.state.balance} className="form-control" style={{ color: 'green' }} id="floatingInvestment" placeholder="Murabox Inversiones" />
                                                <label htmlFor="floatingInvestment">Inversion $</label>
                                            </div>
                                            <div className="form-floating m-auto mt-3"  >
                                                <input type="text" className="form-control" id="floatingNamePlan" placeholder="Murabox Inversiones" />
                                                <label htmlFor="floatingNamePlan">Nombre del Plan</label>
                                                <small className='text-secondary'>* Opcional (en caso q no se asigne, automaticamente asignara la fecha de apertura del mismo como nombre)</small>
                                            </div>
                                            <div className="form-floating m-auto mt-3 border rounded d-flex justify-content-between"  >
                                                <div className="col-md-4">
                                                    <label htmlFor="" className=''><strong>Rangos a Invertir</strong></label>
                                                    <div className="dropdown">
                                                        <button className="btn bg-secondary text-white dropdown-toggle btn-borless" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                                                            Rangos
                                                        </button>
                                                        <ul className="dropdown-menu dropdown-menu-dark" id="list_levels" aria-labelledby="dropdownMenuButton1">
                                                            <li>
                                                                <a className='dropdown-item' onClick={(e) => this.handle_clickLevel(e)}>Rango 1 y 2</a>
                                                            </li>

                                                            <li>
                                                                <a className="dropdown-item" onClick={(e) => this.handle_clickLevel(e)}>Todos los Rangos</a>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                </div>
                                                <div className="col-md-4">
                                                    <h5>{this.state.range_selected}</h5>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="b-example-divider mt-5" />

                                    <div className="list-group">
                                        <div className="list-group-item">
                                            <h3>Seleccion de Monedas</h3>
                                            <h5>Seleccion Maxima <strong>({this.state.max_coins})</strong> / {this.state.coins_selected}</h5>
                                            <h5>Rango Actual: {this.state.current_range}</h5>

                                            <ul id="list_coins" className="list-group">
                                                <li className="list-group-item bg-secondary text-white d-flex">
                                                    <div className="col">
                                                        <input className="form-check-input me-1" type="checkbox" value="bitcoin" checked aria-label="..." />
                                                        <strong>Bitcoin</strong>
                                                    </div>
                                                    <div className="col">
                                                        <strong style={{ color: 'lime' }}>{this.state.price_btc}$</strong>
                                                    </div>
                                                </li>
                                                <li className="list-group-item bg-secondary text-white d-flex">
                                                    <div className="col">
                                                        <input className="form-check-input me-1" type="checkbox" value="etherium" checked aria-label="..." />
                                                        <strong>Etherium</strong>
                                                    </div>
                                                    <div className="col">
                                                        <strong style={{ color: 'lime' }}>{this.state.price_eth}$</strong>
                                                    </div>
                                                </li>
                                                {
                                                    this.coins.map(coin => {
                                                        return (
                                                            <li className="list-group-item d-flex" key={this.state.id}>
                                                                <div className="col">
                                                                    <input className="form-check-input me-1" type="checkbox" value={coin.id} onClick={(e) => this.handle_checked(e)} aria-label="..." />
                                                                    <strong>{coin.name}</strong> <img src={coin.image} alt="image" width="20" height="20" />
                                                                </div>
                                                                <div className="col">
                                                                    <strong style={{ color: 'green' }}>{coin.current_price} $</strong>
                                                                </div>
                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>

                                            <button className='btn btn-primary' onClick={(e) => this.handle_clear(e)}>Clear</button>

                                        </div>
                                    </div>



                                    <div className="b-example-divider mt-5" />

                                    <div className="list-group mb-5">
                                        <div className="list-group-item">
                                            <h3>Preparacion</h3>
                                            <div className="border rounded d-flex">
                                                <div className="col">
                                                    <h5>Rango Normal</h5>
                                                    <input type="range" min="10" max="100" step="5" className="form-range" id="lv_normal" onChange={(e) => this.handle_range(e)} style={{ width: "250px" }}></input>
                                                </div>
                                                <div className="col">
                                                    <h5 style={{ margin: "20px 0 0 70px" }} id="lv_normal">{this.state.lv_normal}%</h5>
                                                </div>
                                            </div>
                                            <div className="border rounded d-flex mt-3">
                                                <div className="col">
                                                    <h5>Rango Intermedio</h5>
                                                    <input type="range" min="10" max="100" step="5" className="form-range" id="lv_intermedio" onChange={(e) => this.handle_range(e)} style={{ width: "250px" }}></input>
                                                </div>
                                                <div className="col">
                                                    <h5 style={{ margin: "20px 0 0 70px" }} id="lv_intermedio">{this.state.lv_intermedio}%</h5>
                                                </div>
                                            </div>
                                            {
                                                this.state.ranges == 2 ?
                                                    <div className="border rounded d-flex justify-content-between mt-3">
                                                        <div className="col">
                                                            <h5>Rango Avanzado</h5>
                                                            <input type="range" min="10" max="100" step="5" className="form-range" id="lv_advance" onChange={(e) => this.handle_range(e)} style={{ width: "250px" }}></input>
                                                        </div>
                                                        <div className="col">
                                                            <h5 style={{ margin: "20px 0 0 70px" }} id="lv_avance">{this.state.lv_advance}%</h5>
                                                        </div>
                                                    </div>
                                                    :
                                                    <div></div>
                                            }

                                            <div id="sumTotalPorcent" className="mt-3">
                                                <h5>Porcentaje Total: {this.state.sum_total_porcent}%</h5>
                                            </div>

                                            <table className="table table-light table-hover mt-5">
                                                <thead>
                                                    <tr>
                                                        <th scope='col'>Rango</th>
                                                        <th scope='col'>Porcentaje</th>
                                                        <th scope='col'>USDT</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>Normal</td>
                                                        <td>{this.state.lv_normal}%</td>
                                                        <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_normal}$</strong></td>
                                                    </tr>
                                                    <tr>
                                                        <td>Intermedio</td>
                                                        <td>{this.state.lv_intermedio}%</td>
                                                        <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_intermedio}$</strong></td>
                                                    </tr>
                                                    {
                                                        this.state.ranges == 2 ?
                                                            <tr>
                                                                <td>Avanzado</td>
                                                                <td>{this.state.lv_advance}%</td>
                                                                <td><strong style={{ color: 'green' }}>{this.state.calc_porcent_advance}$</strong></td>
                                                            </tr>
                                                            :
                                                            <tr></tr>
                                                    }
                                                </tbody>
                                            </table>
                                        </div>
                                        <button className='btn btn-primary mt-3 m-auto' onClick={(e) => this.handle_createPlan(e)}>Crear Plan</button>
                                    </div>
                                </form>

                            </div>
                        </main>
                    </div>
                </div> */}
            </>

        )
    }
}
