import React, { useEffect, useState } from 'react'
import Helmet from 'react-helmet'

import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import Footer from '../components/Footer'
import { getCoins } from '../modules/Coins'
import { formatDolar } from '../components/TablePlan'

export default function MarketCoin() {

    const TITLE = "Market coins"

    const [Coins, setCoins] = useState([]);

    useEffect(() => {
        getCoins().then(res => setCoins(res.coinsFilter));

    }, []);

    return (
        <React.Fragment>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>

            <div className="container-scroller">
                {/* partial:partials/_sidebar.html */}
                <Sidebar />
                {/* partial */}

                <div className="container-fluid page-body-wrapper">
                    {/* partial:partials/_navbar.html */}
                    <Navbar />
                    {/* partial */}

                    <div className="main-panel">
                        <div className="content-wrapper">
                            <div className="row justify-content-center">
                                <div className="col-lg-8 grid-margin stretch-card">
                                    <div className="card overlay">
                                        <div className="card-body">
                                            <h4 className="card-title">Market Coin - Monedas</h4>
                                            <p />
                                            <div className="table-responsive">
                                                <table className="table">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Nº</th>
                                                            <th scope="col">Crypto</th>
                                                            <th scope="col">Precio $</th>
                                                            <th scope="col">24h</th>
                                                        </tr>
                                                    </thead>

                                                    <tbody>
                                                        {
                                                            Coins.map((coin, idx) => {
                                                                return (
                                                                    <tr>
                                                                        <td>{idx + 1}</td>
                                                                        <td><img src={coin.image} width={20} height={20} /> {coin.name} ({coin.symbol})</td>
                                                                        <td><strong style={{ color: 'green' }}>{coin.current_price} $</strong></td>
                                                                        <td className="" style={
                                                                            coin.price_change_percentage_24h < 0.0
                                                                                ? { color: 'red' }
                                                                                : { color: 'green' }
                                                                        }>
                                                                            {
                                                                                formatDolar(coin.price_change_percentage_24h) > 0.0
                                                                                    ? "+"
                                                                                    : ""
                                                                            }
                                                                            {
                                                                                formatDolar(coin.price_change_percentage_24h)
                                                                            }
                                                                            %
                                                                        </td>
                                                                    </tr>
                                                                )
                                                            })
                                                        }
                                                    </tbody>

                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* content-wrapper ends */}

                        {/* partial:partials/_footer.html */}
                        <Footer />
                        {/* partial */}
                    </div>
                    {/* main-panel ends */}
                </div>
                {/* page-body-wrapper ends */}
            </div>
        </React.Fragment>

    )
}
