import React from 'react'
import { Link } from 'react-router-dom';
import Cookies from 'universal-cookie';

export default function NavbarcPanel() {
    const cookies = new Cookies();

    function handleSignOut() {

        if (cookies.get("cpid")) {
            cookies.remove("cpid", {path:"/cpanel"});
            cookies.remove("cpemail", {path:"/cpanel"});
            sessionStorage.clear();
            localStorage.clear();
            window.location.href = "/cpanel"
        }
    }

    return (
        <header>
            <nav className="d-flex navbar navbar-expand-sm navbar-dark p-2 bg-dark" style={{ background: '#50CA28' }}>
                <div >
                    {/* <img src={logo} width={164} height={57} /> */}
                    <Link to="./" style={{ textDecoration: 'none' }}><h2 className='text-white'>cPanel</h2></Link>
                </div>

                <button className="navbar-toggler d-lg-none" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId" aria-expanded="false" aria-label="Toggle navigation" />
                <div className="justify-content-end collapse navbar-collapse" id="collapsibleNavId">
                    <ul className="navbar-nav mx-5">
                        <li className="nav-item active">
                            {
                                !cookies.get("cpid", {path:"/cpanel"}) ?
                                    <Link to="./login" className="nav-link fs-5">
                                        Ingresar
                                    </Link>
                                    :
                                    <button className="nav-link fs-5 " style={{ border: 'none', background: 'none' }} onClick={() => handleSignOut()}>Cerrar Seccion</button>
                            }

                        </li>
                    </ul>

                </div>
            </nav>
        </header>
    )
}
