    //                     <li><hr className="dropdown-divider" /></li>
    //                     <li><a className="dropdown-item" href="#">Dividir entre todo</a></li>
    //                 </ul>
    //             </div>

    //             <form>
    //                 <div className="form-floating mt-3">
    //                     <input type="number" min="50" max="1000" className="form-control" style={{ color: 'green' }} id="floatingAmount" placeholder="Cantidad" onChange={(e) => handle_change(e)} />
    //                     <label htmlFor="floatingfn">Cantidad $</label>
    //                 </div>

    //                 <div className="mt-3">
    //                     <h5>Seleccion: {Selected}. Inversion: <strong style={{ color: 'green' }}>{Balance}$</strong></h5>
    //                 </div>

    //                 <br />

    //                 <div className="border rounded p-2">
    //                     <h5>transferir a la siguiente cuenta de Binance</h5>
    //                     <h6>carlosdiaz@gmail.com</h6>
    //                     <br />
    //                     <h6>Despues de transferir, llenar los datos de transferencia.</h6>
    //                     <small><strong>*</strong> Tener en cuenta que despues de su transferencia en binance, tomar capture, para colocar su referencia en el formulario de abajo</small>
    //                 </div>

    //                 <div className="list-group">
    //                     <div className="list-group-item">
    //                         <h3>Datos de la transferencia Binance</h3>

    //                         <div className="form-floating mt-3">
    //                             <input type="text" className="form-control" id="floatingRef" placeholder="Referencia de la Transaccion" />
    //                             <label htmlFor="floatingRef">Referencia de la Transaccion</label>
    //                         </div>

    //                         <div className="form-floating mt-3">
    //                             <input type="date" className="form-control" id="floatingDate" placeholder="Emision" />
    //                             <label htmlFor="floatingDate">Emision de la Transaccion</label>
    //                         </div>

    //                     </div>
    //                 </div>

