import React, { Component, createRef, useEffect, useState } from 'react'
import Helmet from 'react-helmet'
import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import Footer from '../components/Footer'

import SubHeader from '../components/SubHeader'

import { Link } from 'react-router-dom'

import axios from 'axios'
import PrincipalDataUser from '../components/PrincipalDataUser'

import Cookies from 'universal-cookie'
import { apiURL } from '../modules/Enums'
import { formatDolar } from '../components/TablePlan'
import { differentDate, differentDateLocal, getCurrentDateServer } from '../modules/CPDate'
import { filterCoinsObject, getCoins } from '../modules/Coins'

export default function Dashboard () {
    const TITLE = "Dashboard";
    const cookies = new Cookies();

    if (!cookies.get("id", { path: "./dashboard" })) window.location.href = "../login";

    const [User, setUser] = useState({
        _id: "",
        fullname: "",
        ci: "",
        email: "",
        saldo: {
            holdeo: {
                invest: 0,
                retreat: 0
            }
        },
        plans: {
          holdeo: []
        }
    })

    const [FirstCoins, setFirstCoins] = useState([{name:""},{name:""},{name:""}]);

    const [currentDate, setCurrentDate] = useState("");
    const [PlansTrading, setPlansTrading] = useState([]);
    const [MixPlans, setMixPlans] = useState([])
    
    useEffect(() => {

      //GET CURRENTDATE
      getCurrentDateServer()
        .then(currentDate => setCurrentDate(currentDate.current));

      // GET DATA USER
      axios.get(apiURL + "/users/user/" + cookies.get("id"))
          .then(res => {
            console.log(res.data);
              const { _id, fullname, ci, email, saldo, plans } = res.data;
              setUser({
                  _id: _id,
                  fullname: fullname,
                  ci: ci,
                  email: email,
                  saldo: {
                      holdeo: { invest: saldo.holdeo.invest, retreat: saldo.holdeo.retreat }
                  },
                  plans:{
                    holdeo: plans.holdeo
                  }
              });

              MixPlans.push(plans.holdeo.length != 0 ? plans.holdeo[0] : null);
          })        
      // GET TRADING
      axios.get(apiURL + "/users/user/" + cookies.get("id") + "/plans/trading").then(res => {
        setPlansTrading(res.data)
        MixPlans.push(res.data.length != 0 ? res.data[0] : null);
      });
      
      console.log(MixPlans)
      getCoins().then(res => setFirstCoins(filterCoinsObject(res.coinsFilter, [{name:"Bitcoin"}, {name:"Ethereum"},{name:"BNB"}])))
            
    }, [])
    
    
    function isContent (plans) {
      let is_content = false;

      for (let i = 0; i < plans.length; i++) {
        const plan = plans[i];
        if (plan != null) {
          is_content = true;
          break;
        }
      }
      
    }
    
    return (
        <React.Fragment>
        <Helmet>
            <title>{TITLE}</title>
        </Helmet>
  
        <div className="container-scroller">
  
          {/* partial:partials/_sidebar.html */}
          <Sidebar />
          {/* partial */}
  
          <div className="container-fluid page-body-wrapper">
  
            {/* partial:partials/_navbar.html */}
            <Navbar />
            {/* partial */}
            
            <div className="main-panel">
              <div className="content-wrapper">
                <div className="row">
                  <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <div className="row">
                          <div className="col-9">
                            <div className="d-flex align-items-center align-self-start">
                              <h3 className="mb-0">{formatDolar(User.saldo.holdeo.invest)}$</h3>
                              <p className="text-success ml-2 mb-0 font-weight-medium">
                                USDT
                              </p>
                            </div>
                          </div>
                          <div className="col-3">
                            <div className="icon icon-box-success ">
                              <span className="mdi mdi-cash icon-item" />
                            </div>
                          </div>
                        </div>
                        <h6 className="text-muted font-weight-normal">Saldo de Inversion</h6>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <div className="row">
                          <div className="col-9">
                            <div className="d-flex align-items-center align-self-start">
                              <h3 className="mb-0">{formatDolar(User.saldo.holdeo.retreat)}$</h3>
                              <p className="text-success ml-2 mb-0 font-weight-medium">
                              USDT
                              </p>
                            </div>
                          </div>
                          <div className="col-3">
                            <div className="icon icon-box-success">
                              <span className="mdi mdi-cash icon-item" />
                            </div>
                          </div>
                        </div>
                        <h6 className="text-muted font-weight-normal">
                          Saldo de Retiro
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <div className="row">
                          <div className="col-9">
                            <div className="d-flex align-items-center align-self-start">
                              <h3 className="mb-0">0$</h3>
                              <p className="text-success ml-2 mb-0 font-weight-medium">
                                USDT
                              </p>
                            </div>
                          </div>
                          <div className="col-3">
                            <div className="icon icon-box-success">
                              <span className="mdi mdi-cash icon-item" />
                            </div>
                          </div>
                        </div>
                        <h6 className="text-muted font-weight-normal">
                          Acumulativo Trading
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-3 col-sm-6 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <div className="row">
                          <div className="col-9">
                            <div className="d-flex align-items-center align-self-start">
                              <h3 className="mb-0">0$</h3>
                              <p className="text-success ml-2 mb-0 font-weight-medium">
                                USDT
                              </p>
                            </div>
                          </div>
                          <div className="col-3">
                            <div className="icon icon-box-success ">
                              <span className="mdi mdi-arrow-top-right icon-item" />
                            </div>
                          </div>
                        </div>
                        <h6 className="text-muted font-weight-normal">Acumulativo Minado</h6>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-4 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <h4 className="card-title">Historial de Transacciones</h4>
                        <canvas
                          id="transaction-history"
                          className="transaction-chart"
                        />
                        <div className="bg-gray-dark d-flex d-md-block d-xl-flex flex-row py-3 px-4 px-md-3 px-xl-4 rounded mt-3">
                          <div className="text-md-center text-xl-left">
                            <h6 className="mb-1">Transferencia a Paypal</h6>
                            <p className="text-muted mb-0">
                              07 Jan 2019, 09:12AM
                            </p>
                          </div>
                          <div className="align-self-center flex-grow text-right text-md-center text-xl-right py-md-2 py-xl-0">
                            <h6 className="font-weight-bold mb-0">$236</h6>
                          </div>
                        </div>
                        <div className="bg-gray-dark d-flex d-md-block d-xl-flex flex-row py-3 px-4 px-md-3 px-xl-4 rounded mt-3">
                          <div className="text-md-center text-xl-left">
                            <h6 className="mb-1">Transferencia a Binance</h6>
                            <p className="text-muted mb-0">
                              07 Jan 2019, 09:12AM
                            </p>
                          </div>
                          <div className="align-self-center flex-grow text-right text-md-center text-xl-right py-md-2 py-xl-0">
                            <h6 className="font-weight-bold mb-0">$593</h6>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-8 grid-margin stretch-card">
                    <div className="card overlay">
                      <div className="card-body">
                        <div className="d-flex flex-row justify-content-between">
                          <h4 className="card-title mb-1">Planes Activos</h4>
                          <p className="text-muted mb-1">Mis Planes</p>
                        </div>
                        <div className="row">
                          <div className="col-12">
                            <div className="preview-list">
                              

                                {
                                  !isContent(MixPlans) 
                                  ? 
                                  <Link to="/newplan" style={{textDecoration: 'none'}}>
                                    <span className='text-white p-1 bg-success' >+ Crear Nuevo Plan</span>
                                  </Link>
                                  :
                                  MixPlans.map(plan => {
                                    return (
                                      plan == undefined ? "" :
                                      <div className="preview-item border-bottom">
                                        <div className="preview-thumbnail">
                                          <div className="preview-icon bg-primary">
                                            <i className="mdi mdi-file-document" />
                                          </div>
                                        </div>
                                        <div className="preview-item-content d-sm-flex flex-grow">
                                          <div className="flex-grow">
                                            <h6 className="preview-subject">
                                              {plan.name}
                                            </h6>
                                            <p className="text-muted mb-0">
                                              Sistema CoinProy en {plan.porcentUtility != undefined ? "Trading" : "Holdeo"}
                                            </p>
                                          </div>
                                          <div className="mr-auto text-sm-right pt-2 pt-sm-0">
                                            <p className="text-muted"><strong className='text-success'>{plan.invest}$</strong></p>
                                            <p className="text-muted mb-0">
                                              {plan.porcentUtility != undefined ? "360" : "180"}/{ differentDateLocal(plan[plan.date != undefined ? "date" : "emisionDate"], currentDate) } Dias
                                            </p>
                                          </div>
                                          <div className="col-auto">
                                            {
                                              plan.status != null
                                              ? plan.status == true 
                                                ? <label className="badge badge-success">Ok</label>
                                                :<label className="badge badge-warning">Bad</label>
                                              :
                                              <label className="badge badge-danger">Pendiente</label>
                                            }
                                          </div>
                                        </div>
                                      </div>
                                    )
                                  })
                                }
                                
                                  
                              
                              
                              
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-sm-4 grid-margin">
                  <div className="card">
                      <div className="card-body">
                        <h5>{FirstCoins[0].name}</h5>
                        <div className="row">
                          <div className="col-8 col-sm-12 col-xl-8 my-auto">
                            <div className="d-flex d-sm-block d-md-flex align-items-center">
                              <h2 className="mb-0">${FirstCoins[0].current_price}</h2>
                              <p className="text-success ml-2 mb-0 font-weight-medium" style={
                                FirstCoins[0].price_change_percentage_24h < 0.0
                                ? {color:'red'}
                                : {color:'green'}
                              }>
                                {
                                  formatDolar(FirstCoins[0].price_change_percentage_24h) > 0.0
                                  ? "+"
                                  : ""
                                }
                                {
                                  formatDolar(FirstCoins[0].price_change_percentage_24h)
                                }
                                %
                              </p>
                            </div>
                            <h6 className="text-muted font-weight-normal">
                              MarketCoins de CoinProy
                            </h6>
                          </div>
                          <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                            <img className='icon-lg text-primary ml-auto' src={FirstCoins[0].image} alt="btc" width={70} height={70} />
                            {/* <i className="icon-lg mdi mdi-codepen text-primary ml-auto" /> */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-sm-4 grid-margin">
                  <div className="card">
                      <div className="card-body">
                        <h5>{FirstCoins[1].name}</h5>
                        <div className="row">
                          <div className="col-8 col-sm-12 col-xl-8 my-auto">
                            <div className="d-flex d-sm-block d-md-flex align-items-center">
                              <h2 className="mb-0">${FirstCoins[1].current_price}</h2>
                              <p className="ml-2 mb-0 font-weight-medium" style={
                                FirstCoins[1].price_change_percentage_24h < 0.0
                                ? {color:'red'}
                                : {color:'green'}
                              }>
                                {
                                  formatDolar(FirstCoins[1].price_change_percentage_24h) > 0.0
                                  ? "+"
                                  : ""
                                }
                                {
                                  formatDolar(FirstCoins[1].price_change_percentage_24h)
                                }
                                %
                              </p>
                            </div>
                            <h6 className="text-muted font-weight-normal">
                              MarketCoins de CoinProy
                            </h6>
                          </div>
                          <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                            <img className='icon-lg text-primary ml-auto' src={FirstCoins[1].image} alt="btc" width={70} height={70} />
                            {/* <i className="icon-lg mdi mdi-codepen text-primary ml-auto" /> */}
                          </div>
                        </div>
                      </div>
                  </div>
                  </div>
                  <div className="col-sm-4 grid-margin">
                    <div className="card">
                    <div className="card-body">
                        <h5>{FirstCoins[2].name}</h5>
                        <div className="row">
                          <div className="col-8 col-sm-12 col-xl-8 my-auto">
                            <div className="d-flex d-sm-block d-md-flex align-items-center">
                              <h2 className="mb-0">${FirstCoins[2].current_price}</h2>
                              <p className="ml-2 mb-0 font-weight-medium" style={
                                FirstCoins[2].price_change_percentage_24h < 0.0
                                ? {color:'red'}
                                : {color:'green'}
                              }>
                                {
                                  formatDolar(FirstCoins[2].price_change_percentage_24h) > 0.0
                                  ? "+"
                                  : ""
                                }
                                {
                                  formatDolar(FirstCoins[2].price_change_percentage_24h)
                                }
                                %
                              </p>
                            </div>
                            <h6 className="text-muted font-weight-normal">
                              MarketCoins de CoinProy
                            </h6>
                          </div>
                          <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                            <img className='icon-lg text-primary ml-auto' src={FirstCoins[2].image} alt="btc" width={70} height={70} />
                            {/* <i className="icon-lg mdi mdi-codepen text-primary ml-auto" /> */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                
              </div>
              {/* content-wrapper ends */}
  
              {/* partial:partials/_footer.html */}
              <Footer/>
              {/* partial */}
  
            </div>
            {/* main-panel ends */}
          </div>
          {/* page-body-wrapper ends */}
        </div>
      </React.Fragment>
  
    )
}

