import React from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'
import Helmet from "react-helmet";

import { apiURL } from '../modules/Enums'
import Cookies from 'universal-cookie'

import { Toaster, toast } from 'react-hot-toast'

export default function login() {
    const TITLE = "Login";
    const cookies = new Cookies();

    document.querySelector("body").setAttribute("style", "background-color: #f5f5f5;")

    if (cookies.get("id")) window.location.href = "./dashboard";

    function refreshPage() {
        window.location.reload(false);
    }

    const handle_login = (e) => {
        e.preventDefault();

        const inputs = {
            email: document.querySelector("#floatingEmail").value,
            password: document.querySelector("#floatingPassword").value
        }

        const userPromise = axios.get(apiURL + "/users", { paramsSerializer: { email: inputs.email } })
        toast.promise(userPromise, {
            loading: "Iniciando Sesion",
            success: "Sesion iniciada.",
            error: error => "Error al iniciar sesion. ERROR: " + error.message
        })
        .then(res => {
            const users = res.data;
            return users.filter(user => user.email === inputs.email && user.password === inputs.password)
        })
        .then(user => {
            if (user.length != 0) {
                user = user[0];

                cookies.set("id", user._id, { path: "/" })
                cookies.set("email", user.email, { path: "/" })
                // toast.success("Seccion Iniciada")
                setTimeout(() => {
                    window.location.href = "./dashboard"
                }, 500);
            }
            else {
                document.querySelector("#floatingEmail").value = ""
                document.querySelector("#floatingPassword").value = ""
                toast.error("Usuario no registrado")
            }
        });
            
    }

    return (
        <div className='form'>
            <Toaster />

            <React.Fragment>
                <Helmet>
                    <title>{TITLE}</title>
                    <link rel="stylesheet" href="css/some-styles.css" />
                    <link rel="stylesheet" href="css/style.css" />
                </Helmet>

                <div class="flex items-center min-h-screen p-6">
                    <div class="flex-1 h-full max-w-4xl mx-auto overflow-hidden bg-white rounded-lg shadow-xl dark:bg-gray-800">
                        <div class="flex flex-col overflow-y-auto md:flex-row">
                            <div class="h-32 md:h-auto md:w-1/2">
                                <img
                                    aria-hidden="true"
                                    class="object-cover w-full h-full dark:hidden"
                                    src="img/login-2.jpg"
                                    alt="Office"
                                />
                                <img
                                    aria-hidden="true"
                                    class="hidden object-cover w-full h-full dark:block"
                                    src="img/login-2.jpg"
                                    alt="Office"
                                />
                            </div>
                            <div class="items-center justify-center p-6 sm:p-12 md:w-1/2">
                                <form class="sign-up-form form" action="" method="">
                                    <h1 class="mb-4 text-xl font-semibold text-gray-700 dark:text-gray-200 sp-title">
                                        <span>Entrar</span>
                                    </h1>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Email</p>
                                        <input
                                            class="form-input"
                                            type="email"
                                            placeholder="Introduce tu email"
                                            required=""
                                            id='floatingEmail'
                                        />
                                    </label>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Contraseña</p>
                                        <input
                                            class="form-input"
                                            type="password"
                                            placeholder="Introduce tu contraseña"
                                            required=""
                                            id='floatingPassword'
                                        />
                                    </label>
                                    <label class="form-checkbox-wrapper">
                                        <input class="form-checkbox" type="checkbox" required="" />
                                        <span class="form-checkbox-label">
                                            Recuérdame la próxima vez
                                        </span>
                                    </label>
                                    <button class="site-btn sb-solid-dark" onClick={(e) => handle_login(e)}>
                                        Entrar
                                    </button>
                                    <label class="form-label-wrapper mt-2">
                                        <span class="form-checkbox-label colorb">
                                            <Link to="/account-recovery" rel="noopener noreferrer">
                                                ¿Olvidó su contraseña?
                                            </Link>
                                        </span>
                                    </label>
                                    <label class="form-label-wrapper">
                                        <span class="form-checkbox-label colorb">
                                            <Link to="/register" rel="noopener noreferrer">
                                                Crear Cuenta
                                            </Link>
                                        </span>
                                    </label>
                                    <label class="form-label-wrapper">
                                        <span
                                            class="form-checkbox-label colorb"
                                            onClick={refreshPage}
                                        >
                                            <Link to="/" rel="noopener noreferrer">
                                                <b>Volver</b>
                                            </Link>
                                        </span>
                                    </label>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>


            {/* <main className="mt-5 border rounded p-2 bg-light" style={{ width: '150%' }}>
                <form method="POST" id="formLogin">
                    <div className="text-center">
                        <a href="/" style={{ color: 'black', textDecoration: 'none' }}><h1>CryptoHMR</h1></a>
                    </div>
                    <h1 className="h3 mb-3 fw-normal text-center">Iniciar Seccion</h1>
                    <div className="form-floating">
                        <input type="email" className="form-control" name="inp_email" id="floatingEmail" placeholder="name@example.com" />
                        <label htmlFor="floatingEmail">Correo Electronico</label>
                    </div>
                    <div className="form-floating mt-2">
                        <input type="password" className="form-control" id="floatingPassword" placeholder="Contraseña" />
                        <label htmlFor="floatingPassword">Contraseña</label>
                    </div>
                    <div className="mt-2">
                        <button className="w-100 btn btn-lg btn-mbx text-white" style={{ background: "#d35d5d" }} name="btn_submit" type="submit" onClick={(e) => handle_login(e)}>Ingresar</button>
                        <Link to="/register">Registrarse</Link>
                        <br />
                        <Link to="#">Olvido contraseña</Link>

                        <p className="mt-5 mb-3 text-muted">v 0.00</p>
                    </div>
                </form>
            </main> */}

        </div>
    )
}
