import axios from 'axios';
import React, { useState } from 'react'

import { Toaster, toast } from 'react-hot-toast'
import Cookies from 'universal-cookie';
import MenuCodeConfirm from '../../components/MenuCodeConfirm'
import { apiURL } from '../../modules/Enums';

import "../../css/bootstrap.css"

export default function CPLogin() {

    const [mail, setMail] = useState("");
    const [ID, setID] = useState("")

    const cookies = new Cookies();

    if (cookies.get("cpid", { path: "/cpanel" })) window.location.href = "../cpanel/dashboard/" + cookies.get("id", { path: "/cpanel" });

    async function handleSingin(e) {
        e.preventDefault();

        const inpID = document.querySelector("#floatingID").value;
        const inpPass = document.querySelector("#floatingPassword").value;

        const res = await axios.get(apiURL + "/users/user/" + inpID);

        if (res.data != null) {
            if (res.data.role === "admin") {
                if (inpPass === res.data.password) {
                    document.querySelector("#formLogin").classList.add("d-none");
                    document.querySelector("#formData").classList.remove("d-none");
                    setMail(res.data.email);
                    setID(inpID);
                    toast("Confirma correo para ingresar", { duration: 1500 });
                    return
                }
                else toast.error("Datos Invalidos")
            }
            else toast.error("Datos Invalidos")
        }
        else toast.error("Datos Invalidos")
    }


    return (
        <div className='form'>
            <Toaster />
            <div className="mt-5 p-2 border rounded m-auto" style={{width: '500px'}}>
                <form id="formLogin">
                    <h1>cPanel Login</h1>
                    <div className="form-floating">
                        <input type="text" className="form-control" name="inp_id" id="floatingID" placeholder="123" />
                        <label htmlFor="floatingID">ID</label>
                    </div>
                    <div className="form-floating mt-2">
                        <input type="password" className="form-control" id="floatingPassword" placeholder="Contraseña" />
                        <label htmlFor="floatingPassword">Contraseña</label>
                    </div>
                    <div className="mt-2">
                        <button className="w-100 btn btn-lg btn-mbx text-white" style={{ background: "#d35d5d" }} name="btn_submit" type="submit" onClick={(e) => handleSingin(e)} >Ingresar</button>
                        <p className="mt-5 mb-3 text-muted">v 0.00</p>
                    </div>
                </form>
                <MenuCodeConfirm
                    href={"./cpanel/dashboard/" + ID}
                    mail={mail}
                    id={ID}
                    optionsCookies={{ path: "/cpanel" }}
                    keysCookies={{ id: "cpid", email: "cpemail" }}
                />
            </div>
            {/* <main className="mt-5 border rounded p-2 bg-light" style={{ width: '150%' }}>
                <form id="formLogin">
                    <h1 className="h3 mb-3 fw-normal text-center">cPanel Login</h1>
                    <div className="form-floating">
                        <input type="text" className="form-control" name="inp_id" id="floatingID" placeholder="123" />
                        <label htmlFor="floatingID">ID</label>
                    </div>
                    <div className="form-floating mt-2">
                        <input type="password" className="form-control" id="floatingPassword" placeholder="Contraseña" />
                        <label htmlFor="floatingPassword">Contraseña</label>
                    </div>
                    <div className="mt-2">
                        <button className="w-100 btn btn-lg btn-mbx text-white" style={{ background: "#d35d5d" }} name="btn_submit" type="submit" onClick={(e) => handleSingin(e)} >Ingresar</button>
                        <p className="mt-5 mb-3 text-muted">v 0.00</p>
                    </div>
                </form>

                

            </main> */}

        </div>

    )
}
