import axios from 'axios';
import React, { useEffect, useState } from 'react'
import toast, { Toaster } from 'react-hot-toast';
import Cookies from 'universal-cookie';
import { apiURL } from '../../modules/Enums';
import CPanelDataGlobal from './CPanelDataGlobal'

export default function CPanelTopupAny() {
    const cookies = new Cookies();

    const [solicitud, setSolicitud] = useState({});

    useEffect(() => {
        const solicitud = JSON.parse(sessionStorage.getItem("solicitudSelected"));
        setSolicitud(solicitud);
    }, [])

    async function handleInvest(e) {
        e.preventDefault();

        const res = await axios.get(apiURL + "/users/user/" + solicitud.userId)
        let user = res.data;

        const data = {
            saldo: {
                holdeo: user.saldo.holdeo,
                minado: user.saldo.minado,
                resguardo: user.saldo.resguardo
            }
        }

        user = null;

        data.saldo.holdeo.invest += solicitud.amount;

        const putPromise = axios.put(apiURL + "/users/user/" + solicitud.userId, data);
        toast.promise(putPromise, {
            loading: "Recargando Saldo de: " + solicitud.email,
            success: solicitud.email + " Recarga Exitosa!",
            error: err => "Error al recargar su saldo. ERROR " + err.message
        }, { duration: 3000 })

        axios.delete(apiURL + `/api/admin/${cookies.get("cpid", { path: "/cpanel" })}/2525/solicituds/invest/${solicitud._id}`)
        setTimeout(() => {
            window.location.href = "../topup"
        }, 2000);
    }

    return (
        <>
            <Toaster />

            <div className="border rounded p-3">
                <form>
                    <h4>Recarga de Saldo</h4>
                    <div className="row">
                        <div className="col">
                            <strong>Email:</strong>
                            <br />
                            <span>{solicitud.email}</span>
                        </div>
                        <div className="col">
                            <strong>Nombre:</strong>
                            <br />
                            <span>{solicitud.fullname}</span>
                        </div>
                        <div className="col">
                            <strong>Referencia:</strong>
                            <br />
                            <span>{solicitud.ref}</span>
                        </div>
                        <div className="col">
                            <strong>Monto:</strong>
                            <br />
                            <span style={{ color: 'green' }}>{solicitud.amount}$</span>
                        </div>
                    </div>

                    <br />
                    <span><strong>Verificar el monto que haya llegado antes de recargar</strong></span>
                    <br />
                    <button type="submit" className='btn btn-success' onClick={(e) => handleInvest(e)}>Recargar</button>
                </form>
            </div>
        </>
    )
}
