import React, { useEffect, useState } from 'react'


export default function CPanelSolicitudsPlans({plans}) {
    
    function handleClickPlan (e, plan) {
        e.preventDefault();

        sessionStorage.setItem("planSelected", JSON.stringify(plan));
        
        window.location.href += "/" + plan.userId
    }
    
    return (
        <table className="table table-hover table-striped mt-5">
            <thead>
                <tr>
                    <th scope='col'>#</th>
                    <th scope='col'>Email</th>
                    <th scope='col'>Inversion</th>
                    <th scope='col'>Emitido</th>
                </tr>
            </thead>
            <tbody>
                {
                    plans.map((plan, idx) => {
                        return (
                            <tr key={idx} onClick={(e) => handleClickPlan(e, plan)}>
                                <td>{idx+1}</td>
                                <td>{plan.email}</td>
                                <td><strong style={{color:'green'}}>{plan.invest}$</strong></td>
                                <td>{plan.emisionDate}</td>
                            </tr>
                        )
                    })
                }
            </tbody>
        </table>
    )
}
