import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { apiURL } from '../modules/Enums';
import Cookies from 'universal-cookie';
import Helmet from 'react-helmet';

import Navbar from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import Footer from '../components/Footer';

import TablePlan from '../components/TablePlan';

export default function MyPlansHoldeo() {
    const TITLE = "Mis Planes - Holdeo"

    const [holdeo, setHoldeo] = useState([]);
    const cookies = new Cookies();

    useEffect(() => {

        const getDataUser = async () => {
            const res = await axios.get(apiURL + "/users/user/" + cookies.get("id"));
            const user = res.data;
            console.log(user);
            setHoldeo(user.plans.holdeo);
        }

        getDataUser();

    }, []);



    return (

        <React.Fragment>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>



            <div className="container-scroller">
                <Sidebar />

                <div className="container-fluid page-body-wrapper">
                    <Navbar />

                    <div className="main-panel">
                        <div className="content-wrapper">
                            <div className="row">
                                <div className="col-lg-12 grid-margin stretch-card">
                                    <div className="card overlay">
                                        <div className="card-body">
                                            <h4 className="card-title">Mis Planes - Holdeo</h4>
                                            <p />
                                            <TablePlan plans={holdeo} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <Footer />
                    </div>
                </div>
            </div>
        </React.Fragment>

    )
}
