import React from 'react'
import { BrowserRouter, Link, Routes, Route } from 'react-router-dom';

import Cookies from 'universal-cookie';
import CPanelUser from '../../components/cPanel/CPanelUser';
import CPanelUsers from '../../components/cPanel/CPanelUsers';
import CPanelAccount from './CPanelAccount';
import CPanelHoldeo from './CPanelHoldeo';
import CPanelHoldeoUser from './CPanelHoldeoUser';
import CPanelSolicitudes from './CPanelSolicitudes';

export default function CPanel() {

    const cookies = new Cookies()
    
    if (!cookies.get("cpid")) window.location.href = "../.././cpanel";

    return (
        <div className="container-fluid">
            <div className="row">
                <nav id="sidebarMenu" className="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse sb-dash" style={{ height: "790px" }}>
                    <div className="position-sticky pt-3">
                        <ul className="nav flex-column">
                            <li className="nav-item">
                                <Link to="./users" className="nav-link list-group-item list-group-item-action">
                                    Usuarios
                                </Link>
                            </li>
                            <li className="nav-item mt-2">
                                <Link to="./accounts" className="nav-link list-group-item list-group-item-action">
                                    Cuentas
                                </Link>
                            </li>
                            <li className="nav-item mt-2">
                                <Link to="./solicitudes" className="nav-link list-group-item list-group-item-action">
                                    Solicitudes
                                </Link>
                            </li>
                            <li className="nav-item mt-2">
                                <Link to="./holdeo" className="nav-link list-group-item list-group-item-action">
                                    Holdeo
                                </Link>
                            </li>
                            <li className="nav-item mt-2">
                                <Link to="/investment" className="nav-link list-group-item list-group-item-action">
                                    Minado
                                </Link>
                            </li>
                            <li className="nav-item mt-2">
                                <Link to="/invest" className="nav-link list-group-item list-group-item-action">
                                    Trading
                                </Link>
                            </li>
                        </ul>
                    </div>
                </nav>
                <main className="col-md-9 ms-sm-auto col-lg-10 px-md-4"><div className="chartjs-size-monitor"><div className="chartjs-size-monitor-expand"><div className /></div><div className="chartjs-size-monitor-shrink"><div className /></div></div>
                    <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                        <h3>Dashboard</h3>
                    </div>
                    
                    <Routes>
                        <Route path="/users" element={<CPanelUsers />} />
                        <Route path="/users/user/:id/*" element={<CPanelUser />} />
                        <Route path="/holdeo" element={<CPanelHoldeo />} />
                        <Route path='/holdeo/user/:id/*' element={<CPanelHoldeoUser />} />
                        <Route path="/solicitudes/*" element={<CPanelSolicitudes />} />
                        <Route path="/accounts" element={<CPanelAccount />} />
                    </Routes>
                </main>
            </div>
        </div>

    )
}
