import axios from 'axios'
import React, { useEffect, useState } from 'react'
import toast, { Toaster } from 'react-hot-toast'
import { Link } from 'react-router-dom'
import Cookies from 'universal-cookie'
import { apiURL } from '../../modules/Enums'

export default function CPanelHoldeo(props) {

    const [users, setUsers] = useState([])

    async function getUsers() {
        const res = await axios.get(apiURL + "/users/");
        setUsers(res.data);
        return res;
    }

    useEffect(() => {

        toast.promise(getUsers(), {
            loading: "Cargando usuarios...",
            success: "Usuarios cargados",
            error: (res) => "Error al cargar los usuarios: " + res.message,
        })

    }, []);

    function handleClickUser(e, user) {
        e.preventDefault();

        sessionStorage.setItem("userSelected", JSON.stringify(user));

        window.location.href = "./holdeo/user/" + user._id
    }

    return (
        <>
            <Toaster />
            <div className="container">
                <h4>Usuarios Holdeando</h4>

                <table className='table table-hover table-striped mt-5'>
                    <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nombre</th>
                            <th scope="col">Email</th>
                            <th scope="col">Documento ID</th>
                            <th scope="col">Planes</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            users.length != 0 ? users.map((user, idx) => {
                                return (
                                    user.plans.holdeo.length != 0 ?
                                    <tr key={idx} onClick={(e) => handleClickUser(e, user)} >
                                        <td>{idx + 1}</td>
                                        <td>{user.fullname}</td>
                                        <td>{user.email}</td>
                                        <td>{user.ci}</td>
                                        <td>{user.plans.holdeo.length}</td>
                                    </tr>
                                    :
                                    ''
                                )
                            })
                                :
                                <h4 className='bg-warning' style={{ position: 'absolute' }}>Cargando usuarios</h4>
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
