import React, { useEffect, useState } from 'react'
import Helmet from 'react-helmet'

import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import Footer from '../components/Footer'
import axios from 'axios'
import { apiURL } from '../modules/Enums'
import Cookies from 'universal-cookie'
import { getCurrentDateServer } from '../modules/CPDate'
import { formatDolar } from '../components/TablePlan'
import toast, { Toaster } from 'react-hot-toast'

export default function NewPlanTrading() {
    const cookies = new Cookies();

    const TITLE = "New Plan - Trading"

    const [User, setUser] = useState({
        saldo: {
            holdeo: {
                invest: 0
            }
        }
    })

    const [CurrentDate, setCurrentDate] = useState("00/00/0000");
    const [InputInvest, setInputInvest] = useState(0);
    const [InputNamePlan, setInputNamePlan] = useState("");
    const [readyCreate, setReadyCreate] = useState(false);

    const porcentUtility = 4.4;

    useEffect(() => {

        axios.get(apiURL + "/users/user/" + cookies.get("id")).then(res => setUser(res.data));
        getCurrentDateServer().then(res => setCurrentDate(res.current));

        const intervalID = setInterval(() => {
            validFields();
        }, 500);


    }, [])

    function calcDividendoTotal(invest, porcent) {
        const dividendo = (invest * porcent) / 100;
        return dividendo;
    }

    function handleChangeInput(e) {
        const value = e.target.value;
        setInputInvest(value);
    }

    function handleChangeInputNamePlan(e) {
        const value = e.target.value;
        setInputNamePlan(value);
    }

    function validFields() {
        if (document.querySelector("#exampleInputNamePlan").value != "" && document.querySelector("#exampleInputInvest").value != 0) setReadyCreate(true);
        else setReadyCreate(false)
    }

    async function handleCreateNewPlan(e) {
        e.preventDefault();

        if (InputInvest >= 100) {
            const data = {
                userId: cookies.get("id"),
                email: cookies.get("email"),
                invest: InputInvest,
                name: InputNamePlan,
                emisionDate: null,
                porcentUtility: 0,
                state:"0"
            }

            const promiseCreatePlan = axios.post(apiURL + `/users/user/${cookies.get("id")}/plans/trading`, data);
            toast.promise(promiseCreatePlan, {
                loading: "Creando Plan. Trading...",
                success: "Su plan se ha creado correctamente",
                error: err => "Error al crear plan. ERROR: " + err.message
            }, { duration: 2000 })
                .then(res => {
                    const solicitud = {
                        userId: cookies.get("id"),
                        email: cookies.get("email"),
                        invest: InputInvest,
                        emisionDate: null,
                        porcentUtility: 0,
                        state: "0"
                    }

                    const promiseSolicitud = axios.post(apiURL + "/api/solicituds/trading", solicitud);
                    toast.promise(promiseSolicitud, {
                        loading: "Enviando solicitud a Comproy...",
                        success: "Solicitud enviada, su plan se activara en breves momentos.",
                        error: err => "Error al solicitar plan, ERROR: " + err.message
                    }, { duration: 3000 }).then(res => {
                        axios.get(apiURL + "/users/user/" + cookies.get("id"))
                            .then(res => {
                                console.log("user", res.data)
                                let user = res.data;
                                user.saldo.holdeo.invest -= InputInvest;
                                axios.put(apiURL + "/users/user/" + cookies.get("id"), user);
                                user = null;

                                setTimeout(() => {
                                    window.location.reload();
                                }, 3000);
                            })
                    })
                })
        }
        else toast.error("Minimo no cumplido: su inversion es inferior a 100$. para operar en este plan debe superar o igualar el minimo (100$)",
        {duration: 5000});

    }

    return (
        <React.Fragment>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>

            <Toaster />
            <div className="container-scroller">

                {/* partial:partials/_sidebar.html */}
                <Sidebar />
                {/* partial */}

                <div className="container-fluid page-body-wrapper">

                    {/* partial:partials/_navbar.html */}
                    <Navbar />
                    {/* partial */}

                    <div className="main-panel">
                        <div className="content-wrapper">

                            <div className="row">

                                <div className="col-md-6 grid-margin stretch-card">
                                    <div className="card overlay">
                                        <div className="card-body">
                                            <h4 className="card-title">Asignación</h4>
                                            <p className="card-description"> Asignar nombre e inversion al plan que ira a crear </p>
                                            <form className="forms-sample">
                                                <div className="form-group">
                                                    <label htmlFor="exampleInputNamePlan">
                                                        Nombre del Plan
                                                    </label>
                                                    <input
                                                        type="text"
                                                        min='0'

                                                        className="form-control"
                                                        id="exampleInputNamePlan"
                                                        placeholder="Nombre del Plan"
                                                        onChange={(e) => handleChangeInputNamePlan(e)}
                                                    />
                                                </div>
                                                <div className="form-group">
                                                    <label htmlFor="exampleInputInvest">
                                                        Inversion
                                                    </label>
                                                    <input
                                                        type="number"
                                                        min='0'
                                                        max={User.saldo.holdeo.invest}
                                                        className="form-control"
                                                        id="exampleInputInvest"
                                                        placeholder="Inversion"
                                                        onChange={(e) => handleChangeInput(e)}
                                                    />
                                                </div>

                                            </form>
                                        </div>
                                        <div className="d-flex justify-content-center mb-3">
                                            {
                                                readyCreate
                                                    ? <button className='btn btn-success' onClick={(e) => handleCreateNewPlan(e)}>Crear</button>
                                                    : <span>Complete los campos del formulario!</span>
                                            }
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-6 grid-margin stretch-card">
                                    <div className="card overlay">
                                        <div className="card-body">
                                            <h4 className="card-title">Previsualizador</h4>
                                            <p className="card-description"> Previsualizador de resultados segun los campos del paso "Asignacion". </p>

                                            <span>Fecha de Inicio: {CurrentDate}</span><br />
                                            <span>Plazo de Culminación: 1 Año</span><br /><br />
                                            <span>% de Utilidad anual: {porcentUtility}% ({formatDolar(porcentUtility * 12)}%)</span><br /><br />
                                            <div className="row">
                                                <span>Dividendo:</span>
                                                <div className="col">
                                                    <span>Total: </span><br />
                                                    <strong style={{ color: 'green' }}>{formatDolar(calcDividendoTotal(InputInvest, porcentUtility * 12))}$</strong>
                                                </div>
                                                <div className="col">
                                                    <span>
                                                        Mensual:
                                                    </span><br />
                                                    <strong style={{ color: 'green' }}>{formatDolar(calcDividendoTotal(InputInvest, porcentUtility * 12) / 12)}$</strong>
                                                </div>
                                                <div className="col">
                                                    <span>
                                                        Diario:
                                                    </span><br />
                                                    <strong style={{ color: 'green' }}>{formatDolar(calcDividendoTotal(InputInvest, porcentUtility * 12) / 360)}$</strong>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>

                        </div>
                        {/* content-wrapper ends */}

                        {/* partial:partials/_footer.html */}
                        <Footer />
                        {/* partial */}

                    </div>
                    {/* main-panel ends */}
                </div>
                {/* page-body-wrapper ends */}
            </div>
        </React.Fragment>

    )

}
