import React from 'react'

export default function CPanelTablePlansHoldeo(props) {

    function handleClickPlan (e, plan) {
        e.preventDefault();

        sessionStorage.setItem("planSelected", JSON.stringify(plan));
        
        window.location.href += "/plan/" + plan._id;
    }
    
    return (
        <>
            <h4>Planes</h4>
            <div className="border-bottom"></div>

            <table className='table table-hover table-striped mt-5'>
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Inversion</th>
                        <th scope="col">Rango</th>
                        <th scope="col">Emision</th>
                        <th scope="col">Status</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        props.user.plans.holdeo.length != 0 ? props.user.plans.holdeo.map((plan, idx) => {
                            return (
                                <tr key={idx} onClick={(e) => handleClickPlan(e, plan)}>
                                    <td>{idx + 1}</td>
                                    <td>{plan.name}</td>
                                    <td><strong style={{ color: 'green' }}>{plan.invest}$</strong></td>
                                    <td>{plan.rangeSelected == 2 ? "Todos los rangos" : "Normal y Intermedio"}</td>
                                    <td>{plan.date}</td>
                                    {
                                        plan.status ? <td className='bg-success text-white'>Ok</td>
                                            : <td className='bg-warning'>Bad</td>
                                    }
                                </tr>
                            )
                        })
                            :
                            <h4>No tiene ningun plan</h4>
                    }
                </tbody>
            </table>
        </>
    )
}
