import React, { useEffect, useState } from 'react'
import Helmet from 'react-helmet'
import { Link } from 'react-router-dom'

import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import Footer from '../components/Footer'
import { apiURL } from '../modules/Enums'
import Cookies from 'universal-cookie'
import axios from 'axios'
import { differentDate } from '../modules/CPDate'


export default function MyPlansTrading() {
    const cookies = new Cookies();
    const TITLE = "Mis Planes - Trading SIA"

    const [Plans, setPlans] = useState([]);

    useEffect(() => {

        async function getPlans() {
            let plans = []

            const res = await axios.get(apiURL + "/users/user/" + cookies.get("id") + "/plan/trading/user")
            plans = res.data;

            for (let i = 0; i < plans.length; i++) {
                const diff = await differentDate(plans[i].emisionDate);
                plans[i].diffDate = diff
            }

            setPlans(plans)
        }

        getPlans();

    }, [])

    function handleState (state) {
        switch (state) {
            case "0":
                return (<i className='badge badge-danger'>Stopped</i>)
        
            case "1":
                return (<i className='badge badge-success'>Running</i>)
        }
    }

    return (
        <React.Fragment>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>

            <div className="container-scroller">
                <Sidebar />

                <div className="container-fluid page-body-wrapper">
                    <Navbar />

                    <div className="main-panel">
                        <div className="content-wrapper">
                            <div className="row">
                                <div className="col-lg-12 grid-margin stretch-card">
                                    <div className="card overlay">
                                        <div className="card-body">
                                            <h4 className="card-title">Mis Planes - Trading S.I.A</h4>

                                            <div className="table-responsive">
                                                <table className='table'>
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">#</th>
                                                            <th scope="col">Nombre</th>
                                                            <th scope="col">Inversion</th>
                                                            <th scope="col">Fecha de Emision</th>
                                                            <th scope="col">Transcurrido</th>
                                                            <th scope="col">Estado</th>
                                                            <th scope="col">Accion</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            Plans.map((plan, idx) => (
                                                                <tr>
                                                                    <td>{idx + 1}</td>
                                                                    <td>{plan.name}</td>
                                                                    <td>{plan.invest}</td>
                                                                    <td>{plan.emisionDate}</td>
                                                                    <td>360/{plan.diffDate}</td>
                                                                    <td>{handleState(plan.state)}</td>
                                                                    <td><i className='badge badge-info'>Cerrar Plan</i></td>
                                                                </tr>
                                                            ))
                                                        }
                                                    </tbody>
                                                </table>
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <Footer />
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
