import React from "react";
import Helmet from "react-helmet";

export default function Carousel() {
    return (
        <React.Fragment>
            {/* Hero section start */}
            <section className="hero-section">
                {/* left social link ber */}
                <div className="left-bar">
                    <div className="left-bar-content">
                        <div className="social-links">
                            <a href="#0">
                                <i className="fa fa-telegram" />
                            </a>
                            <a href="#0">
                                <i className="fa fa-instagram" />
                            </a>
                            <a href="#0">
                                <i className="fa fa-discord" />
                            </a>
                        </div>
                    </div>
                </div>
                {/* hero slider area */}
                <div className="hero-slider">
                    <div className="hero-slide-item set-bg" data-setbg="img/bg-4.jpg">
                        <div className="slide-inner">
                            <div className="slide-content">
                                <h2>
                                    Descubre como invertir con COINPROY Facil,
                                    <br />
                                    Rapido y Seguro
                                    <br />
                                </h2>
                                <a href="#0" className="site-btn sb-solid-dark">
                                    Leer Más
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="hero-slide-item set-bg" data-setbg="img/bg-1.jpg">
                        <div className="slide-inner">
                            <div className="slide-content">
                                <h2>
                                    Holdeo de <br /> Criptomonedas
                                </h2>
                                <a href="#0" className="site-btn sb-solid-dark">
                                    Leer Más
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="hero-slide-item set-bg" data-setbg="img/bg-2.jpg">
                        <div className="slide-inner">
                            <div className="slide-content">
                                <h2>
                                    Traiding <br />
                                    S.I.A.
                                    <br />
                                </h2>
                                <a href="#0" className="site-btn sb-solid-dark">
                                    Leer Más
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="hero-slide-item set-bg" data-setbg="img/bg-3.jpg">
                        <div className="slide-inner">
                            <div className="slide-content">
                                <h2>
                                    Minado en <br />
                                    la Nube <br />
                                </h2>
                                <a href="#0" className="site-btn sb-solid-dark">
                                    Leer Más
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="slide-num-holder" id="snh-1" />
                <div className="hero-right-text">CriptoFinanzas</div>
            </section>
            {/* Hero section end */}

            <Helmet>
                <script type="text/javascript" src="js/jquery-2.1.4.min.js"></script>
                <script type="text/javascript" src="js/bootstrap.min.js"></script>
                <script type="text/javascript" src="js/isotope.pkgd.min.js"></script>
                <script type="text/javascript" src="js/owl.carousel.min.js"></script>
                <script type="text/javascript" src="js/jquery.owl-filter.js"></script>
                <script type="text/javascript" src="js/magnific-popup.min.js"></script>
                <script type="text/javascript" src="js/circle-progress.min.js"></script>
                <script type="text/javascript" src="js/main.js"></script>
            </Helmet>
        </React.Fragment>
    );
}
