import React from 'react'
import Helmet from 'react-helmet'
import { Link, Route, Routes } from 'react-router-dom'
import CPanelSolicitudsPlans from '../../components/cPanel/CPanelSolicitudsPlans'
import CPanelTopup from '../../components/cPanel/CPanelTopup'

import { apiURL } from '../../modules/Enums'

export default function CPanelNotification() {


    return (

        <div className='container'>
            <Helmet>
                <title>cPanel - Solicitudes</title>
            </Helmet>

            <div className="row">
                <div className="col">
                    <Link to="./topup">
                        <button className='btn btn-success'>Recarga de saldo</button>
                    </Link>
                </div>
                <div className="col">
                    <Link to="./plans">
                        <button className='btn btn-warning'>Planes</button>
                    </Link>
                </div>
                <div className="col">
                    <Link to="">
                        <button className='btn btn-primary'>Retirar saldo</button>
                    </Link>
                </div>
            </div>
            
            <Routes>
                <Route path="/topup/*" element={<CPanelTopup />} />
                <Route path="/plans/*" element={<CPanelSolicitudsPlans />} />
                <Route path="/retreat/*" element={<CPanelTopup />} />
            </Routes>
            
        </div>


    )
}
