import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Helmet from "react-helmet";

import axios from 'axios'
import Cookies from 'universal-cookie'
import md5 from 'md5'
import { apiURL } from '../modules/Enums';
import toast, { Toaster } from 'react-hot-toast'
import MenuCodeConfirm from '../components/MenuCodeConfirm';

export default function Register() {
    const TITLE = "Crear Cuenta"

    const [Mail, SetMail] = useState("")
    const [User, setUser] = useState({ id: "", email: "" });
    const [confirmMail, setConfirmMail] = useState(false)

    async function handle_register(e) {
        e.preventDefault();

        const fullname = document.querySelector("#floatingfn").value
        const ci = document.querySelector("#floatingCi").value
        const email = document.querySelector("#floatingEmail").value
        const password = document.querySelector("#floatingPassword").value
        const termsChecked = document.querySelector("#box_termine").checked;

        if (fullname == "" || ci == "" || email == "" || password == "") {
            toast.error("Campos sin rellenar");
            return;
        }

        if (!termsChecked) {
            toast.error("Acepte los terminos y condiciones para registrarse en CryptoHMR");
            return;
        }

        const res = await axios.get(apiURL + "/users");
        const exist = res.data.filter(user => user.email == email)

        if (exist.length != 0) {
            toast.error("Ya existe esta cuenta")
            document.querySelectorAll("input").forEach(element => element.value = "");
            document.querySelector("#box_termine").checked = false;
            return;
        }

        const cookies = new Cookies();

        // cookies.set("codeMailVerify", Number.parseInt(Math.random() * 1000000), { path:"/", expires:  })

        setUser({ id: res.data._id, email: email });
        SetMail(email);
        setConfirmMail(true);
        document.querySelector("#formData").classList.add("d-none");
        document.querySelector("#formConfirmMail").classList.remove("d-none");
    }

    function handleConfirmCode(e) {
        e.preventDefault();

        const inpCode = md5(Number.parseInt(document.querySelector("#floatingCode").value));

        if (inpCode == codeConfirm) {
            const fullname = document.querySelector("#floatingfn").value
            const ci = document.querySelector("#floatingCi").value
            const email = document.querySelector("#floatingEmail").value
            const password = document.querySelector("#floatingPassword").value

            const resPromise = axios.post(apiURL + "/users", {
                fullname: fullname,
                ci: ci,
                email: email,
                password: password,
            });

            toast.promise(resPromise, {
                loading: "Estamos creando su cuenta...",
                success: "Bienvenido a CoinProy. Gracias registrarse en nuestra plataforma.",
                error: error => "Error al crear cuenta. ERROR: " + error.message
            }, { duration: 3000 })
                .then(res => {
                    setTimeout(() => {
                        window.location.href = "./login";
                    }, 3500);
                })
        }
        else toast.error("El codigo de verificacion no es valido");
    }

    const [codeConfirm, setCodeConfirm] = useState("");

    function handleSendMail(e) {
        e.preventDefault();

        const resPromise = axios.post(apiURL + "/mailer/sendConfirmCode", { email: Mail });
        toast.promise(resPromise, {
            loading: "Enviando su codigo de verificacion",
            success: "Codigo de verificacion enviado",
            error: error => "error al enviar su codigo ERROR " + error.message
        })
            .then(res => {
                if (res.status == 200) {
                    let code = res.data.code;
                    code = md5(code)
                    setCodeConfirm(code);
                    code = null;
                }
            })

    }


    return (
        <>
            <Toaster />

            <React.Fragment>
                <Helmet>
                    <title>{TITLE}</title>
                    <link rel="stylesheet" href="css/some-styles.css" />
                </Helmet>

                <div class="flex items-center min-h-screen p-6 " id='formData'>
                    <div class="flex-1 h-full max-w-4xl mx-auto overflow-hidden bg-white rounded-lg shadow-xl dark:bg-gray-800">
                        <div class="flex flex-col overflow-y-auto md:flex-row">
                            <div class="h-32 md:h-auto md:w-1/2">
                                <img
                                    aria-hidden="true"
                                    class="object-cover w-full h-full dark:hidden"
                                    src="img/login-1.jpg"
                                    alt="Office"
                                />
                                <img
                                    aria-hidden="true"
                                    class="hidden object-cover w-full h-full dark:block"
                                    src="img/login-1.jpg"
                                    alt="Office"
                                />
                            </div>
                            <div class="items-center justify-center p-6 sm:p-12 md:w-1/2">
                                <form class="sign-up-form form" action="" method="">
                                    <h1 class="mb-4 text-xl font-semibold text-gray-700 dark:text-gray-200 sp-title">
                                        Crear <span>Cuenta</span>
                                    </h1>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Nombre Completo</p>
                                        <input
                                            class="form-input"
                                            type="text"
                                            placeholder="Introduce tu nombre"
                                            required=""
                                            id='floatingfn'
                                        />
                                    </label>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Documento de Identidad</p>
                                        <input
                                            class="form-input"
                                            type="number"
                                            placeholder="Número de documento"
                                            required=""
                                            id='floatingCi'
                                        />
                                    </label>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Email Asociado a Binance</p>
                                        <input
                                            class="form-input"
                                            type="email"
                                            placeholder="Introduce tu email"
                                            required=""
                                            id='floatingEmail'
                                        />
                                    </label>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Contraseña</p>
                                        <input
                                            class="form-input"
                                            type="password"
                                            placeholder="Introduce tu contraseña"
                                            required=""
                                            id='floatingPassword'
                                        />
                                    </label>
                                    <label class="form-label-wrapper">
                                        <p class="form-label">Repetir Contraseña</p>
                                        <input
                                            class="form-input"
                                            type="password"
                                            placeholder="Repite tu contraseña"
                                            required=""
                                            id='floatingPassword2'
                                        />
                                    </label>
                                    <label class="form-checkbox-wrapper">
                                        <input class="form-checkbox" type="checkbox" id='box_termine' required="" />
                                        <span class="form-checkbox-label ">
                                            Acepto los{" "}
                                            <a href="http://" rel="noopener noreferrer">
                                                Términos y Condiciones
                                            </a>
                                        </span>
                                    </label>
                                    <button class="site-btn sb-solid-dark" onClick={(e) => handle_register(e)}>
                                        <a class="colorb2" href="#URL">
                                            Crear
                                        </a>
                                    </button>
                                    <label class="form-label-wrapper mt-2">
                                        <span class="form-checkbox-label colorb">
                                            <Link to="/login" rel="noopener noreferrer">
                                                ¿Ya tiene una cuenta? Acceder
                                            </Link>
                                        </span>
                                    </label>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>

            </React.Fragment>

            {/* <MenuCodeConfirm
                id={User.id}
                mail={User.email}
                href="/login"
                keysCookies={User}
                optionsCookies={{path:"/"}}
            /> */}

            <div class="flex items-center min-h-screen p-6 d-none" id='formConfirmMail'>
                <div class="flex-1 h-full max-w-4xl mx-auto overflow-hidden bg-white rounded-lg shadow-xl dark:bg-gray-800">
                    <div class="flex flex-col overflow-y-auto md:flex-row">
                        <div class="h-32 md:h-auto md:w-1/2">
                            <img
                                aria-hidden="true"
                                class="object-cover w-full h-full dark:hidden"
                                src="img/login-1.jpg"
                                alt="Office"
                            />
                            <img
                                aria-hidden="true"
                                class="hidden object-cover w-full h-full dark:block"
                                src="img/login-1.jpg"
                                alt="Office"
                            />
                        </div>
                        <div class="items-center justify-center p-6 sm:p-12 md:w-1/2">
                            <form class="sign-up-form form" action="" method="">
                                <h1 class="mb-4 text-xl font-semibold text-gray-700 dark:text-gray-200 sp-title">
                                    Confirmar Correo electronico
                                </h1>
                                <span>El codigo se enviara a <strong>{User.email}</strong></span>
                                <div className="m-3" >
                                    <span>Enviar codigo a su correo electronico click en <button className='border' style={{ backgroundColor: 'gainsboro', border: 'none' }} onClick={(e) => handleSendMail(e)}>Enviar</button></span>
                                </div>
                                <label class="form-label-wrapper">
                                    <p class="form-label">Codigo. Ejemplo: "123456"</p>
                                    <input
                                        class="form-input"
                                        type="number"
                                        placeholder="Número de documento"
                                        required=""
                                        id='floatingCode'
                                    />
                                </label>
                                <button class="site-btn sb-solid-dark" onClick={(e) => handleConfirmCode(e)}>
                                    <a class="colorb2" href="./">
                                        Confirmar
                                    </a>
                                </button>

                            </form>
                        </div>
                    </div>
                </div>
            </div>


            {/* <div className='form'>
                <main className="mt-5 border rounded p-2 mb-5 bg-light" style={{ width: '90%' }}>
                    <form id="formData" className=''>
                        <div className="text-center">
                            <Link to="/" style={{ color: 'black', textDecoration: 'none' }}><h1>CryptoHMR</h1></Link>
                        </div>
                        <h1 className="h3 mb-3 fw-normal text-center">Registrarse</h1>
                        <div className="form-floating">
                            <input type="text" className="form-control" name="floatingfn" id="floatingfn" placeholder="Murabox Inversiones" />
                            <label htmlFor="floatingfn">Nombre completo</label>
                        </div>
                        <div className="form-floating mt-2">
                            <input type="text" className="form-control" name="floatingCi" id="floatingCi" placeholder="00.000.000" />
                            <label htmlFor="floatingCi">Cedula de Identidad</label>
                        </div>
                        <div className="form-floating mt-2">
                            <input type="email" className="form-control" name="floatingEmail" id="floatingEmail" placeholder="name@example.com" />
                            <label htmlFor="floatingEmail">Email asociado a su binance</label>
                        </div>
                        <div className="form-floating mt-2">
                            <input type="password" className="form-control" id="floatingPassword" placeholder="Contraseña" />
                            <label htmlFor="floatingPassword">Contraseña</label>
                        </div>
                        <div className="form-floating mt-2">
                            <input type="password" className="form-control" id="floatingPassword2" placeholder="Contraseña" />
                            <label htmlFor="floatingPassword2">Repita Contraseña</label>
                        </div>
                        <div className="mt-2">
                            <input type="checkbox" style={{ marginRight: "10px" }} id="box_termine" />
                            <label htmlFor="" style={{ color: 'blue', textDecoration: 'underline' }} data-bs-toggle="modal" data-bs-target="#modalTemines" >Terminos y Condiciones de CryptoHMR</label>

                            <button className="w-100 btn btn-lg text-white mt-5" style={{ background: "#d35d5d" }} name="btn_submit" type="submit" onClick={(e) => handle_register(e)}>Registrarse</button>
                            <p className="mt-5 mb-3 text-muted">v 0.00</p>
                        </div>
                    </form>

                    <form id="formConfirmMail" className='d-none'>
                        <div className="text-center">
                            <h1>CryptoHMR</h1>
                        </div>
                        <h1 className="h3 mb-3 fw-normal text-center">Confirmar Correo electronico</h1>
                        <span>El codigo se enviara a <strong>{Mail}</strong></span>
                        <div className="m-3" >
                            <span>Enviar codigo a su correo electronico click en <button className='border' style={{ backgroundColor: 'gainsboro', border: 'none' }} onClick={(e) => handleSendMail(e)}>Enviar</button></span>
                        </div>

                        <div className="form-floating mt-3">
                            <input type="text" className="form-control" id="floatingCode" placeholder="Codigo ejemplo: 123456" />
                            <label htmlFor="floatingCode">Codigo ejemplo: 123456</label>
                        </div>
                        <button className="w-100 btn btn-lg text-white mt-5" style={{ background: "#d35d5d" }} name="btn_submit" type="submit" onClick={(e) => handleConfirmCode(e)}>Confirmar</button>
                        <p className="mt-5 mb-3 text-muted">v 0.00</p>
                    </form>

                </main>

                <div class="modal fade" id="modalTemines" tabindex="-1" role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
                    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title">Terminos y Condiciones. <strong>CryptoHMR</strong></h5>
                                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div class="modal-body">
                                <div class="container-fluid">
                                    <p>
                                        Lorem ipsum dolor sit amet consectetur adipisicing elit. Soluta voluptates aliquid nobis delectus impedit fugit error ab eius dignissimos. Nemo nisi facere incidunt magnam corporis ducimus sed, nam harum sunt!
                                    </p>
                                </div>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cerrar</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> */}

        </>


    )
}
