import axios from 'axios';
import React, { useEffect } from 'react'
import toast, { Toaster } from 'react-hot-toast'
import { apiURL } from '../../modules/Enums';

export default function CPanelFormSendMail({ user }) {


    function handleSendMail(e) {
        e.preventDefault();

        const sendData = {
            to: user.email,
            email: document.querySelector("#floatingEmail").value,
            subject: document.querySelector("#floatingSubject").value,
            text: document.querySelector("#text").value,
            fullname: user.fullname
        }

        const sendPromise = axios.post(apiURL + "/mailer/send", sendData);

        toast.promise(sendPromise, {
            loading: "Enviando correo a " + user.email,
            success: "El correo se ha enviado correctamente",
            error: error => "Error al enviar el correo. ERROR: " + error.message
        })
        .then(res => console.log("res", res))
    }


    return (
        <>
            <Toaster />
            <form className='border rounded p-3' style={{ width: "50%", marginLeft: "220px" }}>
                <h4>Enviar Correo</h4>
                <span>Para:</span>
                <div className="form-floating">
                    <input type="email" className="form-control" id="floatingEmail" disabled value={user.email} />
                    <label htmlFor="floatingEmail">Correo Electronico</label>
                </div>

                <div className="form-floating mt-3">
                    <input type="text" className="form-control" id="floatingSubject" placeholder='titulo del correo'/>
                    <label htmlFor="floatingSubject">Subject</label>
                </div>

                <div className="form-floating mt-3">
                    <span>Texto:</span>
                    <textarea id="text" cols="61" rows="5"></textarea>
                </div>

                <button className='btn btn-success' onClick={(e) => handleSendMail(e)}>Enviar</button>

            </form>
        </>
    )
}
