import axios from 'axios'
import React, { useEffect, useState } from 'react'
import toast, { Toaster } from 'react-hot-toast'
import { Route, Routes } from 'react-router-dom'

import { apiURL } from '../../modules/Enums'
import { formatDolar } from '../TablePlan'
import CPanelDataGlobal from './CPanelDataGlobal';

export default function CPanelUsers() {

    const [users, setUsers] = useState([]);

    useEffect(() => {

        const usersPromise = axios.get(apiURL + "/users")
        toast.promise(usersPromise, {
            loading: "Cargando los usuarios",
            success: "Usuarios cargados",
            error: error => "Error al cargar los usuarios. ERROR: " + error.message
        }, { duration: 2000 })
            .then(res => {
                setUsers(res.data);
            })

    }, [])

    function handleClickUser(e, user) {
        e.preventDefault();
        sessionStorage.setItem("userSelected", JSON.stringify(user));

        window.location.href = "./users/user/" + user._id
    }

    return (
        <>
            <Toaster />
            <h4>Usuarios de CoinProy</h4>
            <table className='table table-hover table-striped mt-5'>
                <thead>
                    <tr>
                        <th scope='col'>#</th>
                        <th scope='col'>Nombre completo</th>
                        <th scope='col'>Documento ID</th>
                        <th scope='col'>Email</th>
                        <th scope='col'>Saldo</th>
                        <th scope='col'>Saldo Retiro</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        users.map((user, idx) => {
                            return (
                                <tr key={idx} onClick={(e) => handleClickUser(e, user)}>
                                    <td>{idx + 1}</td>
                                    <td>{user.fullname}</td>
                                    <td>{user.ci}</td>
                                    <td>{user.email}</td>
                                    <td><strong style={{ color: 'green' }}>{formatDolar(user.saldo.holdeo.invest)}$</strong></td>
                                    <td><strong style={{ color: 'green' }}>{formatDolar(user.saldo.holdeo.retreat)}$</strong></td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>

        </>
    )
}
