import React,{useState, useEffect} from 'react'
import axios from 'axios';
import Cookies from 'universal-cookie';
import { apiURL } from '../../modules/Enums';

export default function CPanelTableTopup() {

    const cookies = new Cookies();

    const [solicituds, setSolicituds] = useState([])

    useEffect(() => {
        axios.get(apiURL + `/api/admin/${cookies.get("cpid", {path:"/"})}/2525/solicituds/invest`)
            .then(res => setSolicituds(res.data));
    }, []);

    function handleClickUser (e, solicitud) {
        e.preventDefault();

        sessionStorage.setItem("solicitudSelected", JSON.stringify(solicitud));

        window.location.href = "./topup/" + solicitud._id;
    }
    
    return (
        <table className='table table-hover table-striped'>
            <thead>
                <tr>
                    <th scope='col'>#</th>
                    <th scope='col'>Nombre</th>
                    <th scope='col'>email</th>
                    <th scope='col'>Monto</th>
                    <th scope='col'>Ref</th>
                    <th scope='col'>Emitido</th>
                </tr>
            </thead>

            <tbody>
                {
                    solicituds.length != 0?
                    solicituds.map((solicitud, idx) => {
                        return (
                            <tr key={idx} onClick={(e) => handleClickUser(e, solicitud)}>
                                <td>{idx + 1}</td>
                                <td>{solicitud.fullname}</td>
                                <td>{solicitud.email}</td>
                                <td><strong style={{ color: 'green' }}>{solicitud.amount}$</strong></td>
                                <td>{solicitud.ref}</td>
                                <td>{solicitud.emisionDate}</td>
                            </tr>
                        )
                    })
                    :
                    <h4 className='bg-warning p-2' style={{position:"absolute"}}>No hay Solicitudes Pendientes</h4>
                }
            </tbody>

        </table>

    )
}
