import axios from "axios";
import { apiURL } from "./Enums";

export async function differentDate(d1) {

    const diff = await axios.get(apiURL + "/date/current")
        .then(res => {
            const date = res.data;

            let day1 = changeDateFormat(d1);

            day1 = new Date(day1);
            let day2 = new Date(date.current);

            var difference = Math.abs(day2 - day1);
            const days = difference / (1000 * 3600 * 24)
            return days;

        })

    return diff;

}

export function differentDateLocal (d1, d2) {
    let day1 = changeDateFormat(d1);

    day1 = new Date(day1);
    let day2 = new Date(d2);

    var difference = Math.abs(day2 - day1);
    const days = difference / (1000 * 3600 * 24)
    return days;
}

export async function getCurrentDateServer() {
    const res = await axios.get(apiURL + "/date/current")
    return res.data;
}

export function changeDateFormat(date) {
    const arr_current = date.split("/");
    date = arr_current[1] + "/" + arr_current[0] + "/" + arr_current[2];
    return date;
}

export async function getPorcentTheTableRangeDate(emisionDate) {

    const porcent = differentDate(emisionDate)
        .then(diff => {
            let porcent = 0;

            if (diff <= 30) porcent = 0;
            if (diff > 30 && diff <= 60) porcent = 10;
            if (diff > 60 && diff <= 120) porcent = 8;
            if (diff > 120 && diff <= 180) porcent = 6;
            if (diff > 180) porcent = 4;
            // if (diff >= 0 && diff <= 3) porcent = 10;

            return porcent;
        });

    return porcent;
}

