import React from 'react'
import Helmet from 'react-helmet'

import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import Footer from '../components/Footer'

import imgB from '../assets/binance.png'
import imgMine from '../assets/cloud-network.png'
import imgTrading from '../assets/trading.png'
import { Link } from 'react-router-dom'

export default function NewPlan() {

    const TITLE = "New Plan - Selection"

    return (
        <React.Fragment>
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>

            <div className="container-scroller">

                {/* partial:partials/_sidebar.html */}
                <Sidebar />
                {/* partial */}

                <div className="container-fluid page-body-wrapper">

                    {/* partial:partials/_navbar.html */}
                    <Navbar />
                    {/* partial */}

                    <div className="main-panel">
                        <div className="content-wrapper">
                            <div className="row">


                                {/* HOLDEO */}
                                <div className="col-sm-4 grid-margin">
                                    <Link to="/newplan-holdeo" className='text-decoration-none text-white'>

                                        <div className="card">
                                            <div className="card-body">
                                                <h5>¡Holdea Criptomonedas de la manera mas facil!</h5>
                                                <div className="row">
                                                    <div className="col-8 col-sm-12 col-xl-8 my-auto">
                                                        <div className="d-flex d-sm-block d-md-flex align-items-center">
                                                            <h2 className="mb-0">Holdeo</h2>
                                                            {/* <p className="text-success ml-2 mb-0 font-weight-medium">
                                                            aaa
                                                        </p> */}
                                                        </div>
                                                        <br />
                                                        <h6 className="text-muted font-weight-normal">
                                                            Sistema de Gestion de planes de Comproy
                                                        </h6>
                                                    </div>
                                                    <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                                                        <img className='icon-lg text-primary ml-auto' src={imgB} alt="img" width={75} height={75} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                </div>

                                {/* MINADO */}
                                <div className="col-sm-4 grid-margin">
                                    <Link to="/newplan-holdeo" className='text-decoration-none text-white'>

                                        <div className="card">
                                            <div className="card-body">
                                                <h5>¡Alquila maquinas para su mineria en nube, Facil!</h5>
                                                <div className="row">
                                                    <div className="col-8 col-sm-12 col-xl-8 my-auto">
                                                        <div className="d-flex d-sm-block d-md-flex align-items-center">
                                                            <h2 className="mb-0">Minado en Nube</h2>
                                                        </div>
                                                        <h6 className="text-muted font-weight-normal">
                                                            Sistema de Gestion de planes de Comproy
                                                        </h6>
                                                    </div>
                                                    <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                                                        <img className='icon-lg text-primary ml-auto' src={imgMine} alt="img" width={70} height={70} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                </div>

                                {/* TRADING */}
                                <div className="col-sm-4 grid-margin">
                                    <Link to="/newplan-trading" className='text-decoration-none text-white'>

                                        <div className="card">
                                            <div className="card-body">
                                                <h5>¡Hacer trading... mejor dejalo con nuestra IA de Comproy!</h5>
                                                <div className="row">
                                                    <div className="col-8 col-sm-12 col-xl-8 my-auto">
                                                        <div className="d-flex d-sm-block d-md-flex align-items-center">
                                                            <h2 className="mb-0">Trading S.I.A</h2>
                                                        </div>
                                                        <h6 className="text-muted font-weight-normal">
                                                            Sistema de Gestion de planes de Comproy
                                                        </h6>
                                                    </div>
                                                    <div className="col-4 col-sm-12 col-xl-4 text-center text-xl-right">
                                                        <img className='icon-lg text-primary ml-auto' src={imgTrading} alt="img" width={70} height={70} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                </div>



                            </div>
                        </div>
                        {/* content-wrapper ends */}

                        {/* partial:partials/_footer.html */}
                        <Footer />
                        {/* partial */}

                    </div>
                    {/* main-panel ends */}
                </div>
                {/* page-body-wrapper ends */}
            </div>
        </React.Fragment>

    )
}
