import React, { useEffect, useState } from 'react'
import { Toaster } from 'react-hot-toast'
import { Route, Routes } from 'react-router-dom'
import CPanelDataGlobal from './CPanelDataGlobal'
import CPanelFormSendMail from './CPanelFormSendMail'

export default function CPanelUser() {

    const [user, setUser] = useState({})

    useEffect(() => {

        setUser(JSON.parse(sessionStorage.getItem("userSelected")));
        
    }, [])

    console.log(user)
    
    return (
        <>
            <Toaster />
            <CPanelDataGlobal user={user} />
            <Routes>
                <Route path="/sendmail" element={<CPanelFormSendMail user={user} />} />
            </Routes>
        </>
    )
}
