import React, { useRef, useState } from 'react'
import { apiURL, Ranges } from '../modules/Enums';

import { filterCoinsObject, getCoins } from '../modules/Coins'
import TableUtility from './TableUtility';
import { differentDate, getPorcentTheTableRangeDate } from '../modules/CPDate';
import FormClosePlan, { calcWithPorcentToDolar, sumTotalUtility } from './FormClosePlan';
import VisorClosePlan from './VisorClosePlan';
import axios from 'axios';
import Cookies from 'universal-cookie';
import toast, { Toaster } from 'react-hot-toast';

export function formatDolar(dolar) {
    let strDolar = dolar + "";
    let indexPoint = strDolar.indexOf(".");
    return strDolar.substr(0, indexPoint == -1 ? 6 : indexPoint + 3)
}

export default function TablePlan(props) {

    const [planClicked, setPlanClicked] = useState({
        invest: "",
        name: "",
        rangeSelected: "",
        coinsNormal: [],
        coinsIntermedio: [],
        coinsAdvance: [],
        porcents: {},
        coinsFilterCurrentPricesNormal: [],
        coinsCurrentPricesIntermedio: [],
        coinsCurrentPricesAdvance: []
    });

    const [currentPrices, setCurrentPrices] = useState([]);
    const [discount, setDiscount] = useState(0);
    const [totalRetreat, setTotalRetreat] = useState(0);
    const [PorcentDiscount, setPorcentDiscount] = useState(0)
    const [planClickedId, setPlanClickedId] = useState(-1)


    const closePlan = {
        discount: 0,
        totalRetreat: 0,
        porcentDiscount: 0
    };

    /* SE USARA PARA MOSTRAR LOS DIAS TRASCURRIDOS */
    const [diffDays, setDiffDays] = useState("");
console.log("props", props)
    async function handleClickPlan(planClicked) {
        console.log("Plan Clicked", planClicked);

        const plan = planClicked;

        const rangeSelected = plan.rangeSelected == 2 ? Ranges[2] : Ranges[1];
        const status = plan.status ? "Ok" : "Bad";

        const objPlan = {
            name: plan.name,
            invest: plan.invest,
            rangeSelected: rangeSelected,
            coinsNormal: plan.coins.normal,
            coinsIntermedio: plan.coins.intermedio,
            coinsAdvance: plan.coins.advance,
            porcents: plan.porcents,
            date: plan.date,
            status: status,
            discount: 0,
            totalRetreat: 0,
            PorcentDiscount: 0
        }

        const res = await getCoins();

        const coinsFilterCurrentPricesIntermedio = filterCoinsObject(res.coinsFilter, plan.coins.intermedio);
        const coinsFilterCurrentPricesAdvance = plan.rangeSelected == 2 ? filterCoinsObject(res.coinsFilter, plan.coins.advance) : [];
        const coinsFilterCurrentPricesNormal = filterCoinsObject(res.coinsFilter, [{ name: "Bitcoin" }, { name: "Ethereum" }])

        objPlan.coinsFilterCurrentPricesNormal = coinsFilterCurrentPricesNormal;
        objPlan.coinsCurrentPricesIntermedio = coinsFilterCurrentPricesIntermedio;
        objPlan.coinsCurrentPricesAdvance = coinsFilterCurrentPricesAdvance;

        differentDate(objPlan.date)
            .then(days => setDiffDays(days));

        sessionStorage.setItem("planClickedId", plan._id);
        setPlanClicked(objPlan);
        setCurrentPrices(res.currentPrices);

        calcStop(objPlan.invest);
    }

    async function calcStop(invest) {
        const sumTotal = sumTotalUtility();
        const calcPorcent = (sumTotal / invest) * 100;

        console.log(calcPorcent)

        if (calcPorcent <= 80) {
            const cookies = new Cookies();

            // PLAN GET
            let res = await axios.get(apiURL + "/users/user/" + cookies.get("id") + "/plan/" + sessionStorage.getItem("planClickedId"));
            const plan = res.data;

            if (plan.status) {
                plan.status = false;

                // MODIFICA EL STATUS A BAD
                await axios.put(apiURL + "/users/user/" + cookies.get("id") + "/plan/" + sessionStorage.getItem("planClickedId"), plan);

                // NOTIFICAR AL ADMINISTRADOR
                toast.error("notification axios to admin");

                alert("Su plan '" + planClicked.name + "' se ha puesto en STOP, a bajado un 20% de perdidas. se cambiara al estado Bad");
            }
        }
    }

    function calcPorcentToDolar(rangePorcent, sizeCoins) {
        let calc = (rangePorcent / 100 * planClicked.invest) / sizeCoins;
        return formatDolar(calc);
    }

    function calcPorcentToCrypto(rangePorcent, cryptoPrice, sizeCoins) {
        const calcDolar = calcPorcentToDolar(rangePorcent, sizeCoins);
        const calcCrypto = calcDolar / cryptoPrice;
        return formatCrypto(calcCrypto);
    }

    function formatCrypto(crypto) {
        crypto = crypto + "";
        return crypto.substr(0, 10);
    }

    function handleCloseModal() {
        document.querySelector("#visorClosePlan").classList.add("d-none")
        document.querySelector("#tableHoldeo").classList.add("d-none")
        document.querySelector("#formClosePlan").classList.add("d-none")
        document.querySelector("#formConfirm").classList.add("d-none")

    }

    async function handleToggleShowClosePlan() {
        let subtotal = 0;

        for (let idx = 0; idx < planClicked.coinsNormal.length; idx++) {
            const currentPricesNormal = currentPrices[idx];

            const crypto = calcPorcentToCrypto(planClicked.porcents.normal, planClicked.coinsNormal[idx].price, planClicked.coinsNormal.length)
            const fact = formatDolar(crypto * currentPricesNormal);
            subtotal += Number.parseFloat(fact);
        }

        /* INTERMEDIO */
        for (let idx = 0; idx < planClicked.coinsCurrentPricesIntermedio.length; idx++) {
            const currentPricesIntermedio = planClicked.coinsCurrentPricesIntermedio[idx].current_price;
            const crypto = calcPorcentToCrypto(planClicked.porcents.intermedio, planClicked.coinsIntermedio[idx].price, planClicked.coinsIntermedio.length)
            const fact = formatDolar(crypto * currentPricesIntermedio);
            subtotal += Number.parseFloat(fact);
        }

        /* AVANZADO */
        if (planClicked.coinsAdvance.length > 0) {
            for (let idx = 0; idx < planClicked.coinsCurrentPricesAdvance.length; idx++) {
                const currentPricesAdvance = planClicked.coinsCurrentPricesAdvance[idx].current_price;
                const crypto = calcPorcentToCrypto(planClicked.porcents.advance, planClicked.coinsAdvance[idx].price, planClicked.coinsAdvance.length)
                const fact = formatDolar(crypto * currentPricesAdvance);
                subtotal += Number.parseFloat(fact);
            }
        }

        const porcent = await getPorcentTheTableRangeDate(planClicked.date);

        if (porcent == 0) setDiscount("Nada");
        else {
            const calcDiscount = porcent / 100 * subtotal
            console.log("calc", calcDiscount)
            /* formula: % / 100 * inversion = (% en dolar) */
            setPorcentDiscount(porcent);
            setDiscount(calcDiscount);
            setTotalRetreat(subtotal - calcDiscount);
        }

        document.querySelector("#visorClosePlan").classList.toggle("d-none")
    }

    function handleToggleShowHoldeo() {
        document.querySelector("#tableHoldeo").classList.toggle("d-none")
    }

    function handleClosePlan() {
        document.querySelector("#visorClosePlan").classList.add("d-none")
        document.querySelector("#formClosePlan").classList.remove("d-none");
    }

    return (
        <div className='container'>
            <Toaster />
            <div className="table-responsive">
                <table className='table table-hover ' onClick={(e) => {
                    // const planSelected = e.target.parentElement.children[0].innerText - 1;
                    // // setPlanSelected(planSelected);
                    // handleClickPlan(planSelected);
                }}>
                    <thead>
                        <tr>
                            <th scope='col'>#</th>
                            <th scope='col'>Nombre</th>
                            <th scope='col'>Inversion</th>
                            <th scope='col'>Rango Seleccionado</th>
                            <th scope='col'>Fecha de Emision</th>
                            <th scope='col'>Estado</th>
                            <th scope='col'>Detalles</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            props.plans.length != 0
                                ?
                                props.plans.map((plan, idx) => {
                                    return (
                                        <tr key={idx} data-bs-toggle="modal" data-bs-target="#modelPlan" /* onClick={() => handleClickPlan(plan)} */>
                                            <td>{idx + 1}</td>
                                            <td>{plan.name}</td>
                                            <td><strong style={{ color: 'green' }}>{plan.invest}$</strong></td>
                                            <td>{plan.rangeSelected == "2" ? "Todos los Rangos" : "Rango normal y Intermedio"}</td>
                                            <td>{plan.date}</td>
                                            {
                                                plan.status ?
                                                    <td><label className="badge badge-success">Ok</label></td>
                                                    :
                                                    <td><label className="badge badge-danger">Bad</label></td>
                                            }
                                            <td>
                                                <label for="target-modal" class="btn badge badge-danger" onClick={() => handleClickPlan(plan)}>Ver</label>
                                                <input type="checkbox" id="target-modal" />
                                                <div class="modal">
                                                    <div class="modal-inner" style={{ width: 'inherit' }}>
                                                        <label for="target-modal" class="close-modal"></label>
                                                        {/* CONTENT */}

                                                        <div className="modal-body">
                                                            <div className="container-fluid">
                                                                {/* DATOS LINEAL */}
                                                                <table className='table table-hover '>
                                                                    <thead>
                                                                        <tr>
                                                                            <th scope='col'>Nombre</th>
                                                                            <th scope='col'>Inversion</th>
                                                                            <th scope='col'>Rango Seleccionado</th>
                                                                            <th scope='col'>Fecha de emision</th>
                                                                            <th scope='col'>Estado</th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        <tr>
                                                                            <td>{planClicked.name}</td>
                                                                            <td><strong style={{ color: 'green' }}>{planClicked.invest}$</strong></td>
                                                                            <td>{planClicked.rangeSelected}</td>
                                                                            <td>{planClicked.date}</td>
                                                                            {
                                                                                planClicked.status == "Ok" ?
                                                                                    <td className='bg-success'><strong>Ok</strong></td>
                                                                                    :
                                                                                    <td className='bg-warning'><strong>Bad</strong></td>
                                                                            }
                                                                        </tr>
                                                                    </tbody>
                                                                </table>

                                                                <div className='mb-5 text-center'>
                                                                    <label htmlFor="rangeDays"><strong>Dias holdeado: {diffDays} / 180</strong></label><br />
                                                                    <input type="range" min="0" max="180" step="1" id="rangeDays" style={{ width: "300px" }} value={diffDays} disabled />
                                                                </div>

                                                                <div className="d-flex justify-content-evenly">
                                                                    <button className='btn btn-success' onClick={() => handleToggleShowHoldeo()}>Mostrar Holdeo</button>
                                                                    {
                                                                        diffDays >= 0 /*31*/ && planClicked.status ?
                                                                            diffDays > 180 ? <button id="btn_closePlan" className='btn btn-success' onClick={() => handleToggleShowClosePlan()}>Finalizar Plan</button>
                                                                                :
                                                                                <button id="btn_closePlan" className='btn btn-danger' onClick={() => handleToggleShowClosePlan()}>Desea Cerrar Plan</button>
                                                                            :
                                                                            <span className='bg-secondary p-2 text-white rounded'>Bloqueado</span>
                                                                    }
                                                                </div>

                                                                <h5 className='mt-5'>Total Holdeado: <strong style={{ color: 'green' }}>{formatDolar(sumTotalUtility())}$</strong></h5>

                                                                <div className='d-none mb-5' id="tableHoldeo">

                                                                    <hr className="dropdown-divider mt-3 mb-5" />

                                                                    <h5 className='bg-success p-2 text-white'>Rangos y Monedas</h5>

                                                                    {/* MONEDAS -> NORMAL*/}
                                                                    <table className='table'>
                                                                        <thead>
                                                                            <tr>
                                                                                <td><strong>Normal</strong></td>
                                                                                <td><strong>Moneda</strong></td>
                                                                                <td><strong>Precio Compra</strong></td>
                                                                                <td><strong>Precio Actual</strong></td>
                                                                                <td><strong>Comprado</strong></td>
                                                                                <td><strong>Reparto</strong></td>
                                                                                <td><strong>Monto Actual</strong></td>
                                                                            </tr>
                                                                        </thead>
                                                                        <tbody>
                                                                            {
                                                                                planClicked.coinsNormal.map((coin, idx) => {
                                                                                    return (
                                                                                        <tr >
                                                                                            <td><strong>*</strong></td>
                                                                                            <td><strong>{coin.name}</strong></td>
                                                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {/* PRECIO ACTUAL */}
                                                                                                    {currentPrices[idx]}$
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'sandybrown' }}>
                                                                                                    {calcPorcentToCrypto(planClicked.porcents.normal, planClicked.coinsNormal[idx].price, planClicked.coinsNormal.length)}
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {calcPorcentToDolar(planClicked.porcents.normal, planClicked.coinsNormal.length)}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {formatDolar(currentPrices[idx] * calcPorcentToCrypto(planClicked.porcents.normal, planClicked.coinsNormal[idx].price, planClicked.coinsNormal.length))}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                        </tr>
                                                                                    )
                                                                                })
                                                                            }
                                                                        </tbody>
                                                                    </table>

                                                                    <TableUtility
                                                                        idTag="tb-util-normal"
                                                                        porcent={planClicked.porcents.normal}
                                                                        currentPrices={planClicked.coinsFilterCurrentPricesNormal}
                                                                        coins={planClicked.coinsNormal}
                                                                        invest={planClicked.invest}
                                                                    />

                                                                    <hr className="dropdown-divider mt-5" />

                                                                    {/* MONEDAS -> INTERMEDIO */}

                                                                    <table className='table'>
                                                                        <thead>
                                                                            <tr>
                                                                                <td><strong>Intermedio</strong></td>
                                                                                <td><strong>Moneda</strong></td>
                                                                                <td><strong>Precio Compra</strong></td>
                                                                                <td><strong>Precio Actual</strong></td>
                                                                                <td><strong>Comprado</strong></td>
                                                                                <td><strong>Reparto</strong></td>
                                                                                <td><strong>Monto Actual</strong></td>
                                                                            </tr>
                                                                        </thead>
                                                                        <tbody>
                                                                            {
                                                                                planClicked.coinsIntermedio.map((coin, idx) => {
                                                                                    return (
                                                                                        <tr >
                                                                                            <td><strong>*</strong></td>
                                                                                            <td><strong>{coin.name}</strong></td>
                                                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {/* PRECIO ACTUAL */}
                                                                                                    {planClicked.coinsCurrentPricesIntermedio[idx].current_price}$
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'sandybrown' }}>
                                                                                                    {calcPorcentToCrypto(planClicked.porcents.intermedio, planClicked.coinsIntermedio[idx].price, planClicked.coinsIntermedio.length)}
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {calcPorcentToDolar(planClicked.porcents.intermedio, planClicked.coinsIntermedio.length)}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {formatDolar(planClicked.coinsCurrentPricesIntermedio[idx].current_price * calcPorcentToCrypto(planClicked.porcents.intermedio, planClicked.coinsIntermedio[idx].price, planClicked.coinsIntermedio.length))}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                        </tr>
                                                                                    )
                                                                                })
                                                                            }
                                                                        </tbody>
                                                                    </table>

                                                                    <TableUtility
                                                                        idTag="tb-util-intermedio"
                                                                        porcent={planClicked.porcents.intermedio}
                                                                        currentPrices={planClicked.coinsCurrentPricesIntermedio}
                                                                        coins={planClicked.coinsIntermedio}
                                                                        format={formatDolar}
                                                                        calcPorcentToCrypto={calcPorcentToCrypto}
                                                                        invest={planClicked.invest}
                                                                    />

                                                                    <hr className="dropdown-divider mt-5" />

                                                                    {/* MONEDAS -> AVANZADO */}

                                                                    <table className='table'>
                                                                        <thead>
                                                                            <tr>
                                                                                <td><strong>Avanzado</strong></td>
                                                                                <td><strong>Moneda</strong></td>
                                                                                <td><strong>Precio Compra</strong></td>
                                                                                <td><strong>Precio Actual</strong></td>
                                                                                <td><strong>Comprado</strong></td>
                                                                                <td><strong>Reparto</strong></td>
                                                                                <td><strong>Monto Actual</strong></td>
                                                                            </tr>
                                                                        </thead>
                                                                        <tbody>
                                                                            {
                                                                                planClicked.coinsAdvance.map((coin, idx) => {
                                                                                    return (
                                                                                        <tr >
                                                                                            <td><strong>*</strong></td>
                                                                                            <td><strong>{coin.name}</strong></td>
                                                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {/* PRECIO ACTUAL */}
                                                                                                    {planClicked.coinsCurrentPricesAdvance[idx].current_price}$
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'sandybrown' }}>
                                                                                                    {calcPorcentToCrypto(planClicked.porcents.advance, planClicked.coinsAdvance[idx].price, planClicked.coinsAdvance.length)}
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {calcPorcentToDolar(planClicked.porcents.advance, planClicked.coinsAdvance.length)}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                            <td>
                                                                                                <strong style={{ color: 'green' }}>
                                                                                                    {formatDolar(planClicked.coinsCurrentPricesAdvance[idx].current_price * calcPorcentToCrypto(planClicked.porcents.advance, planClicked.coinsAdvance[idx].price, planClicked.coinsAdvance.length))}
                                                                                                    $
                                                                                                </strong>
                                                                                            </td>
                                                                                        </tr>
                                                                                    )
                                                                                })
                                                                            }
                                                                        </tbody>
                                                                    </table>

                                                                    <TableUtility
                                                                        idTag="tb-util-advance"
                                                                        porcent={planClicked.porcents.advance}
                                                                        currentPrices={planClicked.coinsCurrentPricesAdvance}
                                                                        coins={planClicked.coinsAdvance}
                                                                        format={formatDolar}
                                                                        calcPorcentToCrypto={calcPorcentToCrypto}
                                                                        invest={planClicked.invest}
                                                                    />

                                                                </div>

                                                                <hr className="dropdown-divider mt-5" />

                                                                {/* MODAL CERRAR PLAN */}


                                                                <VisorClosePlan
                                                                    diffDays={diffDays}
                                                                    PorcentDiscount={PorcentDiscount}
                                                                    totalRetreat={totalRetreat}
                                                                    discount={discount}
                                                                />

                                                                <FormClosePlan
                                                                    planClicked={planClicked}
                                                                    PorcentDiscount={PorcentDiscount}
                                                                    diffDays={diffDays}
                                                                />

                                                            </div>
                                                        </div>


                                                    </div>
                                                    <label for="target-modal" class="overlay-modal"></label>
                                                </div>
                                            </td>
                                        </tr>
                                    )

                                })
                                :
                                <h4 className='bg-warning text-dark' style={{ position: 'absolute' }}>No posee ningun plan en su lista de planes</h4>
                        }
                    </tbody>
                </table>

            </div>
            <div className="modal fade" id="modelPlan" tabIndex={-1} role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable modal-xl" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">{planClicked.name}</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                        </div>
                        <div className="modal-body">
                            <div className="container-fluid">
                                {/* DATOS LINEAL */}
                                <table className='table table-hover '>
                                    <thead>
                                        <tr>
                                            <th scope='col'>Nombre</th>
                                            <th scope='col'>Inversion</th>
                                            <th scope='col'>Rango Seleccionado</th>
                                            <th scope='col'>Fecha de emision</th>
                                            <th scope='col'>Estado</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td>{planClicked.name}</td>
                                            <td><strong style={{ color: 'green' }}>{planClicked.invest}$</strong></td>
                                            <td>{planClicked.rangeSelected}</td>
                                            <td>{planClicked.date}</td>
                                            {
                                                planClicked.status == "Ok" ?
                                                    <td className='bg-success'><strong>Ok</strong></td>
                                                    :
                                                    <td className='bg-warning'><strong>Bad</strong></td>
                                            }
                                        </tr>
                                    </tbody>
                                </table>

                                <div className='mb-5 text-center'>
                                    <label htmlFor="rangeDays"><strong>Dias holdeado: {diffDays} / 180</strong></label><br />
                                    <input type="range" min="0" max="180" step="1" id="rangeDays" style={{ width: "300px" }} value={diffDays} disabled />
                                </div>

                                <div className="d-flex justify-content-evenly">
                                    <button className='btn btn-success' onClick={() => handleToggleShowHoldeo()}>Mostrar Holdeo</button>
                                    {
                                        diffDays >= 0 /*31*/ && planClicked.status ?
                                            diffDays > 180 ? <button id="btn_closePlan" className='btn btn-success' onClick={() => handleToggleShowClosePlan()}>Finalizar Plan</button>
                                                :
                                                <button id="btn_closePlan" className='btn btn-danger' onClick={() => handleToggleShowClosePlan()}>Desea Cerrar Plan</button>
                                            :
                                            <span className='bg-secondary p-2 text-white rounded'>Bloqueado</span>
                                    }
                                </div>

                                <h5 className='mt-5'>Total Holdeado: <strong style={{ color: 'green' }}>{formatDolar(sumTotalUtility())}$</strong></h5>

                                <div className='d-none mb-5' id="tableHoldeo">

                                    <hr className="dropdown-divider mt-3 mb-5" />

                                    <h5 className='bg-success p-2 text-white'>Rangos y Monedas</h5>

                                    {/* MONEDAS -> NORMAL*/}
                                    <table className='table table-hover table-striped'>
                                        <thead>
                                            <tr>
                                                <td><strong>Normal</strong></td>
                                                <td><strong>Moneda</strong></td>
                                                <td><strong>Precio Compra</strong></td>
                                                <td><strong>Precio Actual</strong></td>
                                                <td><strong>Comprado</strong></td>
                                                <td><strong>Reparto</strong></td>
                                                <td><strong>Monto Actual</strong></td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                planClicked.coinsNormal.map((coin, idx) => {
                                                    return (
                                                        <tr >
                                                            <td><strong>*</strong></td>
                                                            <td><strong>{coin.name}</strong></td>
                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {/* PRECIO ACTUAL */}
                                                                    {currentPrices[idx]}$
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'sandybrown' }}>
                                                                    {calcPorcentToCrypto(planClicked.porcents.normal, planClicked.coinsNormal[idx].price, planClicked.coinsNormal.length)}
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {calcPorcentToDolar(planClicked.porcents.normal, planClicked.coinsNormal.length)}
                                                                    $
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {formatDolar(currentPrices[idx] * calcPorcentToCrypto(planClicked.porcents.normal, planClicked.coinsNormal[idx].price, planClicked.coinsNormal.length))}
                                                                    $
                                                                </strong>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </table>

                                    <TableUtility
                                        idTag="tb-util-normal"
                                        porcent={planClicked.porcents.normal}
                                        currentPrices={planClicked.coinsFilterCurrentPricesNormal}
                                        coins={planClicked.coinsNormal}
                                        invest={planClicked.invest}
                                    />

                                    <hr className="dropdown-divider mt-5" />

                                    {/* MONEDAS -> INTERMEDIO */}

                                    <table className='table table-hover table-striped'>
                                        <thead>
                                            <tr>
                                                <td><strong>Intermedio</strong></td>
                                                <td><strong>Moneda</strong></td>
                                                <td><strong>Precio Compra</strong></td>
                                                <td><strong>Precio Actual</strong></td>
                                                <td><strong>Comprado</strong></td>
                                                <td><strong>Reparto</strong></td>
                                                <td><strong>Monto Actual</strong></td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                planClicked.coinsIntermedio.map((coin, idx) => {
                                                    return (
                                                        <tr >
                                                            <td><strong>*</strong></td>
                                                            <td><strong>{coin.name}</strong></td>
                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {/* PRECIO ACTUAL */}
                                                                    {planClicked.coinsCurrentPricesIntermedio[idx].current_price}$
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'sandybrown' }}>
                                                                    {calcPorcentToCrypto(planClicked.porcents.intermedio, planClicked.coinsIntermedio[idx].price, planClicked.coinsIntermedio.length)}
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {calcPorcentToDolar(planClicked.porcents.intermedio, planClicked.coinsIntermedio.length)}
                                                                    $
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {formatDolar(planClicked.coinsCurrentPricesIntermedio[idx].current_price * calcPorcentToCrypto(planClicked.porcents.intermedio, planClicked.coinsIntermedio[idx].price, planClicked.coinsIntermedio.length))}
                                                                    $
                                                                </strong>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </table>

                                    <TableUtility
                                        idTag="tb-util-intermedio"
                                        porcent={planClicked.porcents.intermedio}
                                        currentPrices={planClicked.coinsCurrentPricesIntermedio}
                                        coins={planClicked.coinsIntermedio}
                                        format={formatDolar}
                                        calcPorcentToCrypto={calcPorcentToCrypto}
                                        invest={planClicked.invest}
                                    />

                                    <hr className="dropdown-divider mt-5" />

                                    {/* MONEDAS -> AVANZADO */}

                                    <table className='table table-hover table-striped'>
                                        <thead>
                                            <tr>
                                                <td><strong>Avanzado</strong></td>
                                                <td><strong>Moneda</strong></td>
                                                <td><strong>Precio Compra</strong></td>
                                                <td><strong>Precio Actual</strong></td>
                                                <td><strong>Comprado</strong></td>
                                                <td><strong>Reparto</strong></td>
                                                <td><strong>Monto Actual</strong></td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                planClicked.coinsAdvance.map((coin, idx) => {
                                                    return (
                                                        <tr >
                                                            <td><strong>*</strong></td>
                                                            <td><strong>{coin.name}</strong></td>
                                                            <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {/* PRECIO ACTUAL */}
                                                                    {planClicked.coinsCurrentPricesAdvance[idx].current_price}$
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'sandybrown' }}>
                                                                    {calcPorcentToCrypto(planClicked.porcents.advance, planClicked.coinsAdvance[idx].price, planClicked.coinsAdvance.length)}
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {calcPorcentToDolar(planClicked.porcents.advance, planClicked.coinsAdvance.length)}
                                                                    $
                                                                </strong>
                                                            </td>
                                                            <td>
                                                                <strong style={{ color: 'green' }}>
                                                                    {formatDolar(planClicked.coinsCurrentPricesAdvance[idx].current_price * calcPorcentToCrypto(planClicked.porcents.advance, planClicked.coinsAdvance[idx].price, planClicked.coinsAdvance.length))}
                                                                    $
                                                                </strong>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </table>

                                    <TableUtility
                                        idTag="tb-util-advance"
                                        porcent={planClicked.porcents.advance}
                                        currentPrices={planClicked.coinsCurrentPricesAdvance}
                                        coins={planClicked.coinsAdvance}
                                        format={formatDolar}
                                        calcPorcentToCrypto={calcPorcentToCrypto}
                                        invest={planClicked.invest}
                                    />

                                </div>

                                <hr className="dropdown-divider mt-5" />

                                {/* MODAL CERRAR PLAN */}


                                <VisorClosePlan
                                    diffDays={diffDays}
                                    PorcentDiscount={PorcentDiscount}
                                    totalRetreat={totalRetreat}
                                    discount={discount}
                                />

                                <FormClosePlan
                                    planClicked={planClicked}
                                    PorcentDiscount={PorcentDiscount}
                                    diffDays={diffDays}
                                />

                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" data-bs-dismiss="modal" onClick={() => handleCloseModal()}>Aceptar</button>
                        </div>
                    </div>
                </div>
            </div>



        </div>
    )
}
