import React, { useState, useEffect } from 'react'
import { filterCoinsObject, getCoins } from '../../modules/Coins';

export default function CPanelSolicitudsUserPlan() {
    function formatDolar(dolar) {
        let strDolar = dolar + "";
        let indexPoint = strDolar.indexOf(".");
        return strDolar.substr(0, indexPoint == -1 ? 6 : indexPoint + 3)
    }

    function calcPorcentToDolar(rangePorcent, sizeCoins) {
        let calc = (rangePorcent / 100 * plan.invest) / sizeCoins;
        return formatDolar(calc);
    }

    function calcPorcentToCrypto(rangePorcent, cryptoPrice, sizeCoins) {
        const calcDolar = calcPorcentToDolar(rangePorcent, sizeCoins);
        const calcCrypto = calcDolar / cryptoPrice;
        return formatCrypto(calcCrypto);
    }

    function formatCrypto(crypto) {
        crypto = crypto + "";
        return crypto.substr(0, 10);
    }

    const [plan, setPlan] = useState({ coins: { normal: [], intermedio: [], advance: [] } });
    const [currentPricesNormal, setCurrentPricesNormal] = useState([]);
    const [currentPricesIntermedio, setCurrentPricesIntermedio] = useState([]);
    const [currentPricesAdvance, setCurrentPricesAdvance] = useState([]);



    useEffect(async () => {
        const planStorage = JSON.parse(sessionStorage.getItem("planSelected"));
        setPlan(planStorage);

        const res = await getCoins()

        const coinsFilterCurrentPricesNormal = filterCoinsObject(res.coinsFilter, [{ name: "Bitcoin" }, { name: "Ethereum" }])
        const coinsFilterCurrentPricesIntermedio = filterCoinsObject(res.coinsFilter, planStorage.coins.intermedio);
        const coinsFilterCurrentPricesAdvance = planStorage.rangeSelected == 2 ? filterCoinsObject(res.coinsFilter, planStorage.coins.advance) : [];

        let currentNormal = [];
        let currentIntermedio = [];
        const currentAdvance = [];

        coinsFilterCurrentPricesNormal.forEach(coin => currentNormal.push(coin.current_price));
        coinsFilterCurrentPricesIntermedio.forEach(coin => currentIntermedio.push(coin.current_price));
        coinsFilterCurrentPricesAdvance.forEach(coin => currentAdvance.push(coin.current_price))

        setCurrentPricesNormal(currentNormal);
        setCurrentPricesIntermedio(currentIntermedio);
        setCurrentPricesAdvance(currentAdvance);

        // sessionStorage.setItem("currentPricesDolar", JSON.stringify({
        //     Normal: currentNormal,
        //     Intermedio: currentIntermedio,
        //     advance: currentAdvance
        // }));

        // setCurrentPricesAdvance(plan.rangeSelected == 2 ? coinsFilterCurrentPricesAdvance.forEach(coin => {return coin.current_price}) : []);


    }, [])



    return (
        <>
            <div className="row mt-5">
                <div className="col">
                    <h4>Plan: {plan.name}</h4>
                </div>
                <div className="col">
                    <h4>Inversion <strong style={{ color: 'green' }}>{plan.invest}$</strong></h4>
                </div>
                <div className="col">
                    <h4>Inicio: {plan.date}</h4>
                </div>
                <div className="col">
                    <h4>Utilidad: { }</h4>
                </div>
            </div>


            <table className='table table-hover table-striped mt-3'>
                <thead>
                    <tr>
                        <td><strong>Normal</strong></td>
                        <td><strong>Moneda</strong></td>
                        <td><strong>Precio "Comprado"</strong></td>
                        <td><strong>Reparto</strong></td>
                    </tr>
                </thead>
                <tbody>
                    {
                        plan.coins.normal.map((coin, idx) => {
                            return (
                                <tr key={idx}>
                                    <td><strong>*</strong></td>
                                    <td><strong>{coin.name}</strong></td>
                                    <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                    <td>
                                        <strong style={{ color: 'green' }}>
                                            {calcPorcentToDolar(plan.porcents.normal, plan.coins.normal.length)}
                                            $
                                        </strong>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>

            <table className='table table-hover table-striped mt-5'>
                <thead>
                    <tr>
                        <td><strong>Intermedio</strong></td>
                        <td><strong>Moneda</strong></td>
                        <td><strong>Precio "Comprado"</strong></td>
                        <td><strong>Reparto</strong></td>
                    </tr>
                </thead>
                <tbody>
                    {
                        plan.coins.intermedio.map((coin, idx) => {
                            return (
                                <tr key={idx}>
                                    <td><strong>*</strong></td>
                                    <td><strong>{coin.name}</strong></td>
                                    <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                    <td>
                                        <strong style={{ color: 'green' }}>
                                            {calcPorcentToDolar(plan.porcents.intermedio, plan.coins.intermedio.length)}
                                            $
                                        </strong>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>

            <table className='table table-hover table-striped mt-5 mb-5'>
                <thead>
                    <tr>
                        <td><strong>Avanzado</strong></td>
                        <td><strong>Moneda</strong></td>
                        <td><strong>Precio "Comprado"</strong></td>
                        <td><strong>Reparto</strong></td>
                    </tr>
                </thead>
                <tbody>
                    {
                        plan.coins.advance.map((coin, idx) => {
                            return (
                                <tr key={idx}>
                                    <td><strong>*</strong></td>
                                    <td><strong>{coin.name}</strong></td>
                                    <td><strong style={{ color: 'green' }}>{coin.price}$</strong></td>
                                    <td>
                                        <strong style={{ color: 'green' }}>
                                            {calcPorcentToDolar(plan.porcents.advance, plan.coins.advance.length)}
                                            $
                                        </strong>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>



        </>
    )
}
