import { BrowserRouter, Route, Routes } from 'react-router-dom'
import Dashboard from './pages/Dashboard'
import Index from './pages/Index'
import Login from './pages/Login'
import MyAccount from './pages/MyAccount'
import Register from './pages/Register'
import MyInvestment from './pages/MyInvestment'
import NewPlanInvestment from './pages/NewPlanInvestment'
import Invest from './pages/Invest'
import MyPlans from './pages/MyPlans'
import History from './pages/History'

// cPanel
import CPLogin from './pages/cpanel/CPLogin'
import CPanel from './pages/cpanel/CPanel'
import NavbarcPanel from './components/NavbarcPanel'
import MarketCoin from './pages/MarketCoin'
import NewPlan from './pages/NewPlan'
import NewPlanTrading from './pages/NewPlanTrading'
import MyPlansHoldeo from './pages/MyPlansHoldeo'
import MyPlansTrading from './pages/MyPlansTrading'


export default function App() {
        
    return (
        <>
            {/* <Header /> */}
            <BrowserRouter>
                <Routes>
                    <Route path="/" element={<Index />} />
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/dashboard/" element={<Dashboard />} />
                    <Route path="/market-coins" element={<MarketCoin />} />
                    <Route path="/myaccount" element={<MyAccount />} />
                    <Route path="/investment" element={<MyInvestment />} />
                    <Route path="/invest" element={<Invest />} />
                    <Route path="/newplan" element={ <NewPlan /> } />
                    <Route path="/newplan-holdeo" element={ <NewPlanInvestment /> } />
                    <Route path="/newplan-trading" element={ <NewPlanTrading /> } />
                    <Route path="/myplans" element={<MyPlans />} />
                    <Route path="/myplans-holdeo" element={<MyPlansHoldeo />} />
                    <Route path="/myplans-trading" element={<MyPlansTrading />} />
                    <Route path='/history' element={<History />} />
                    
                    <Route path="/cpanel" element={<CPLogin />} />
                    <Route path='/cpanel/dashboard/:id/*' element={[<NavbarcPanel 
                                                                        title="cPanel Dashboard" 
                                                                        keysCookies={{id:"cpid", email:"cpemail"}} 
                                                                        optionsCookies={{path:"/cpanel"}} />, <CPanel />]} />
                    
                    {/* <Route path="/test" exact element={[<Header />, <Testing />]} /> */}
                </Routes>
            </BrowserRouter>
        </>
    )
}