import React from 'react'
import { Routes, Route } from 'react-router-dom'
import CPanelActionUser from './CPanelActionUser'

export default function CPanelDataGlobal(props) {
    return (

        <div className="container">
            <h4>Datos Generales</h4>
            <div className="border-bottom"></div>

            <div className="row mt-3 border rounded p-3">
                <div className="col">
                    <h5>Nombre:</h5>
                    <span><strong>{props.user.fullname}</strong></span>
                </div>
                <div className="col">
                    <h5>Documento ID:</h5>
                    <span><strong>{props.user.ci}</strong></span>
                </div>
                <div className="col">
                    <h5>Email:</h5>
                    <span><strong>{props.user.email}</strong></span>
                </div>
            </div>

            <div className="border-bottom mt-5"></div>

            <div className="mt-5">
                <div className="row">
                    <Routes>
                        <Route path="/" element={<CPanelActionUser />} />
                        <Route path="/plan/:idp" element={
                            <div className='col'>
                                <button className='btn btn-primary'>Analizar</button>
                            </div>
                        } />
                    </Routes>
                </div>
            </div>
        </div>
    )
}
