import React, { Component, useEffect, useState } from 'react'
import axios from 'axios';
import md5 from 'md5'
import Helmet from 'react-helmet';
import Cookies from 'universal-cookie';
import toast, { Toaster } from 'react-hot-toast';
import { apiURL } from '../modules/Enums';

import Navbar from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import Footer from '../components/Footer';
import MenuCodeConfirm from '../components/MenuCodeConfirm'




export default function MyAccount() {
    const TITLE = "Mi Cuenta"
    const cookies = new Cookies();

    const [User, setUser] = useState({
        email: "",
        ci: "",
        fullname: "",
        password: "",
        saldo: {
            holdeo: {
                invest: 0,
                retreat: 0
            }
        }
    })

    useEffect(() => {

        if (!cookies.get("id")) window.location.href = "./login"

        axios.get(apiURL + "/users/user/" + new Cookies().get("id"))
            .then(res => {
                setUser({
                    email: res.data.email,
                    ci: res.data.ci,
                    fullname: res.data.fullname,
                    password: md5(res.data.password),
                    saldo: {
                        holdeo: { invest: res.data.saldo.holdeo.invest, retreat: res.data.saldo.holdeo.retreat }
                    }
                })
            })

    })

    // handle_show() {
    //     const field = document.querySelector("#floatingPassword");

    //     if (field.type === "password") field.type = "text";
    //     else field.type = "password";

    // }

    function handleClickChangePassword(e) {
        e.preventDefault();

        document.querySelector("#formData").classList.remove("d-none");
    }

    function handle_changePassword() {
        // CONTROLA EL CAMBIO DE CLAVE

        const passwords = {
            current: document.querySelector("#exampleInputPasswordCurrent1").value,
            new: document.querySelector("#exampleInputPassword1").value,
            confirm: document.querySelector("#exampleInputConfirmPassword1").value,
        }

        console.log(passwords)

        if (md5(passwords.current) == User.password) {
            if (passwords.new == passwords.confirm) {
                const putPromise = axios.put(apiURL + "/users/user/" + cookies.get("id", { path: "/dashboard" }), {
                    password: passwords.new
                });

                toast.promise(putPromise, {
                    loading: "Cambiando su contraseña...",
                    success: "Su contraseña se ha cambiado exitosamente",
                    error: error => "Error al intentar cambiar su contraseña ERROR: " + error.message
                }, { duration: 2000 })
                    .then(res => {
                        setTimeout(() => {
                            window.location.reload()
                        }, 2000);
                    });
            }
            else {
                toast.error("Error al intentar cambiar Contraseña.")
                setTimeout(() => {
                    window.location.reload()
                }, 2000);
            }
        }
        else {
            toast.error("Error al intentar cambiar Contraseña.")
            setTimeout(() => {
                window.location.reload()
            }, 2000);
        }



    }

    return (
        <React.Fragment>
            <Toaster />
            <Helmet>
                <title>{TITLE}</title>
            </Helmet>
            <div className="container-scroller">
                {/* partial:partials/_sidebar.html */}
                <Sidebar User={User} />
                {/* partial */}

                <div className="container-fluid page-body-wrapper">
                    {/* partial:partials/_navbar.html */}
                    <Navbar User={User} />
                    {/* partial */}

                    <div className="main-panel">
                        <div className="content-wrapper pb-5">
                            <div className=" grid-margin stretch-card">
                                <div className="card overlay">
                                    <div className="card-body">
                                        <h4 className="card-title">Datos Personales</h4>
                                        <p className="card-description"> Protegido por CoinProy </p>
                                        <form className="forms-sample">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputUsername1">
                                                    Full Name
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="exampleInputUsername1"
                                                    placeholder="Username"
                                                    value={User.fullname}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="exampleInputCi">
                                                    Documento ID
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="exampleInputCi"
                                                    placeholder="Documento ID"
                                                    value={User.ci}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="exampleInputEmail1">
                                                    Email address
                                                </label>
                                                <input
                                                    type="email"
                                                    className="form-control"
                                                    id="exampleInputEmail1"
                                                    placeholder="Email"
                                                    value={User.email}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="exampleInputPasswordCurrent1">
                                                    Current Password
                                                </label>
                                                <input
                                                    type="password"
                                                    className="form-control"
                                                    id="exampleInputPasswordCurrent1"
                                                    placeholder="Current Password"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="exampleInputPassword1">
                                                    New Password
                                                </label>
                                                <input
                                                    type="password"
                                                    className="form-control"
                                                    id="exampleInputPassword1"
                                                    placeholder="New Password"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="exampleInputConfirmPassword1">
                                                    Confirm Password
                                                </label>
                                                <input
                                                    type="password"
                                                    className="form-control"
                                                    id="exampleInputConfirmPassword1"
                                                    placeholder="Confirm Password"
                                                />
                                            </div>
                                            <div className="form-check form-check-flat form-check-primary">
                                                <label className="form-check-label">
                                                    <input
                                                        type="checkbox"
                                                        className="form-check-input"
                                                    />{" "}
                                                    Remember me <i className="input-helper" />
                                                </label>
                                            </div>
                                            <button type="button" className="btn btn-primary mr-2" onClick={(e) => handleClickChangePassword(e)}>
                                                Cambiar Password
                                            </button>
                                            <button className="btn btn-dark">Cancel</button>
                                        </form>
                                    </div>
                                </div>
                            </div>

                            <MenuCodeConfirm
                                href="/myaccount"
                                mail={User.email}
                                response={true}
                                callback={handle_changePassword}
                            />
                        </div>
                        {/* content-wrapper ends */}

                        {/* partial:partials/_footer.html */}
                        <Footer />
                        {/* partial */}
                    </div>


                    {/* main-panel ends */}
                </div>
                {/* page-body-wrapper ends */}
            </div>

        </React.Fragment>

    )

    // render() {
    //     return (

    //         // <div >
    //         //     <main>
    //         //         <div className="container text-center col-md-5 border rounded mt-3">
    //         //             <PrincipalDataUser saldo />
    //         //         </div>
    //         //         <div className="list-group">
    //         //             <div className="list-group-item">
    //         //                 <h3>Datos Personales</h3>
    //         //                 <div className="mb-2 border p-4 rounded">
    //         //                     <h5>Nombre Completo</h5>
    //         //                     <input type="text" className="form-control" id="floatingFn" disabled value={this.state.fullname} />
    //         //                 </div>
    //         //                 <div className="mb-2 border p-4 rounded">
    //         //                     <h5>Cedula de identidad</h5>
    //         //                     <input type="text" className="form-control" id="floatingCi" disabled value={this.state.ci} />
    //         //                 </div>
    //         //                 <div className="mb-2 border p-4 rounded">
    //         //                     <h5>Email</h5>
    //         //                     <input type="text" className="form-control" id="floatingEmail" disabled value={this.state.email} />
    //         //                 </div>
    //         //             </div>
    //         //         </div>
    //         //         <div className="b-example-divider" />
    //         //         <div className="list-group">
    //         //             <div className="list-group-item">
    //         //                 <h3>Contraseña</h3>
    //         //                 <div className="mb-2">
    //         //                     <div className="d-flex">
    //         //                         <input type="password" className="form-control" name="inp_password" id="floatingPassword" disabled value={this.state.password} />
    //         //                         <button className="btn btn-secondary" id="btn_view_password" onClick={() => this.handle_show()}>º</button>
    //         //                     </div>
    //         //                 </div>
    //         //                 <div className="form-floating">
    //         //                     <input type="password" className="form-control" name="inp_password2" id="floatingPassword2" />
    //         //                     <label htmlFor="floatingPassword">Contraseña</label>
    //         //                 </div>
    //         //                 <div className="form-floating mt-2">
    //         //                     <input type="password" className="form-control" name="inp_password3" id="floatingPassword3" />
    //         //                     <label htmlFor="floatingPassword">Repita contraseña</label>
    //         //                 </div>
    //         //                 <button type="submit" className="mt-3 btn btn-primary center" onClick={() => this.handle_changePassword()}>Cambiar</button>
    //         //             </div>
    //         //         </div>
    //         //     </main>

    //         // </div>

    //     )
    // }
}