import React, { useEffect, useState } from 'react'
import { sumTotalUtility } from './FormClosePlan';
import { formatDolar } from './TablePlan'

export default function VisorClosePlan({diffDays, PorcentDiscount, discount, totalRetreat}) {

    const [porcentDiscountUpdate, setPorcentDiscountUpdate] = useState(-1);

    function handleClosePlan() {
        document.querySelector("#visorClosePlan").classList.add("d-none")
        document.querySelector("#formClosePlan").classList.remove("d-none");

        sessionStorage.setItem("case", diffDays > 180 ? '1':'2');
    }
    
    return (
        <div className="d-none" id="visorClosePlan">
            <div class="container-fluid">
                <h5>Dias Transcurrido: {diffDays} / 180</h5>
                <h5>Total: <strong style={{ color: 'green' }}>{formatDolar(sumTotalUtility())}$</strong></h5>
                <h5>Disponible para retirar: <strong style={{ color: 'green' }}>{formatDolar(totalRetreat)}$</strong></h5>
                <h5>% descuento: <strong style={{ color: '#e4c51f' }}>{PorcentDiscount}%</strong></h5>
                <h5>
                    descuento USDT: {
                        discount == "Nada" ?
                            <strong style={{ color: 'red' }}>-0$</strong>
                            :
                            <strong style={{ color: 'red' }}>-{formatDolar(discount)}$</strong>
                    }
                </h5>

                <div className="container mt-5">
                    <table className="table table-hover table-striped">
                        <thead className='bg-success text-white'>
                            <tr>
                                <th scope='col'>Tiempo</th>
                                <th scope='col'>Tasa de salida</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>0 a 30 Dias</td>
                                <td><strong>No puede retirar</strong></td>
                            </tr>
                            <tr>
                                <td>31 a 60 Dias</td>
                                <td><strong>10%</strong></td>
                            </tr>
                            <tr>
                                <td>61 a 120 Dias</td>
                                <td><strong>8%</strong></td>
                            </tr>
                            <tr>
                                <td>120 a 180 Dias</td>
                                <td><strong>6%</strong></td>
                            </tr>
                            <tr>
                                <td>181+ Dias</td>
                                <td><strong>4%</strong></td>
                            </tr>
                        </tbody>
                    </table>
                    {
                        diffDays > 180 ? <button id="btn_closePlan" className='btn btn-success' onClick={() => handleClosePlan()}>Finalizar</button>
                        :<button id="btn_closePlan" className='btn btn-danger' onClick={() => handleClosePlan()}>Cerrar Plan</button>
                    }
                </div>


            </div>
        </div>

    )
}
