import React, { useState, useEffect } from 'react'
import Helmet from 'react-helmet';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Cookies from 'universal-cookie';
import { formatDolar } from './TablePlan';
import { apiURL } from '../modules/Enums';


export default function Navbar() {
  const cookies = new Cookies();

  function refreshPage() {
    window.location.reload(false);
  }

  function handleSignOut() {

    if (cookies.get("id")) {
      cookies.remove("id", { path: "/" });
      cookies.remove("email", { path: "/" });
      sessionStorage.clear();
      localStorage.clear();
      window.location.href = "../login"
    }
  }

  const [User, setUser] = useState({
    email: "coinproy@coinproy.com",
    saldo: {
      holdeo: {
        invest: 0,
        retreat: 0
      }
    }
  })

  useEffect(() => {

    axios.get(apiURL + "/users/user/" + cookies.get("id", { path: "/dashboard" }))
      .then(res => setUser(res.data));

  }, [])

  return (
    <React.Fragment>
      <Helmet>
        <link
          rel="stylesheet"
          href="assets/vendors/mdi/css/materialdesignicons.min.css"
        />
        <link
          rel="stylesheet"
          href="assets/vendors/css/vendor.bundle.base.css"
        />
        <link
          rel="stylesheet"
          href="assets/vendors/jvectormap/jquery-jvectormap.css"
        />
        <link
          rel="stylesheet"
          href="assets/vendors/flag-icon-css/css/flag-icon.min.css"
        />
        <link
          rel="stylesheet"
          href="assets/vendors/owl-carousel-2/owl.carousel.min.css"
        />
        <link
          rel="stylesheet"
          href="assets/vendors/owl-carousel-2/owl.theme.default.min.css"
        />
        <link rel="stylesheet" href="assets/css/style.css" />
      </Helmet>
      <nav className="navbar p-0 fixed-top d-flex flex-row">
        <div className="navbar-brand-wrapper d-flex d-lg-none align-items-center justify-content-center">
          <div onClick={refreshPage}>
            <Link to="/dashboard" className="navbar-brand brand-logo-mini" >
              <img src="assets/images/logo_mini.png" alt="logo" />
            </Link>
          </div>
        </div>
        <div className="navbar-menu-wrapper flex-grow d-flex align-items-stretch justify-content-end">
          <button
            className="navbar-toggler navbar-toggler align-self-center"
            type="button"
            data-toggle="minimize"
          >
            <span className="mdi mdi-menu" />
          </button>
          <ul className="navbar-nav w-100">
            <li className="nav-item w-100">
              <form className="nav-link mt-2 mt-md-0 d-none d-lg-flex search">
                <Link type="button" className="btn btn-success btn-fw mr-2" to="/invest">
                  Saldo Inversión <strong>{formatDolar(User.saldo.holdeo.invest)}$</strong>
                </Link>
                <button type="button" className="btn btn-success btn-fw">
                  Saldo Retiro <strong>{formatDolar(User.saldo.holdeo.retreat)}$</strong>
                </button>
              </form>
            </li>
          </ul>
          <ul className="navbar-nav navbar-nav-right">
            <li className="nav-item dropdown d-none d-lg-block">
              <a
                className="nav-link btn btn-success create-new-button"
                id="createbuttonDropdown"
                data-toggle="dropdown"
                aria-expanded="false"
                href="#0"
              >
                + Crear Nuevo Plan
              </a>
              <div
                className="dropdown-menu dropdown-menu-right navbar-dropdown preview-list"
                aria-labelledby="createbuttonDropdown"
              >
                <h6 className="p-3 mb-0">Planes</h6>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-file-outline text-primary" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">Holdeo</p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-web text-info" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">Trading</p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-layers text-danger" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">Minado</p>
                  </div>
                </a>
                <div className="dropdown-divider" />
              </div>
            </li>
            <li className="nav-item dropdown border-left">
              <a
                className="nav-link count-indicator dropdown-toggle"
                id="messageDropdown"
                href="#0"
                data-toggle="dropdown"
                aria-expanded="false"
              >
                <i className="mdi mdi-email" />
                <span className="count bg-success" />
              </a>
              <div
                className="dropdown-menu dropdown-menu-right navbar-dropdown preview-list"
                aria-labelledby="messageDropdown"
              >
                <h6 className="p-3 mb-0">Messages</h6>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <img
                      src="assets/images/faces/face4.jpg"
                      alt="per"
                      className="rounded-circle profile-pic"
                    />
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">
                      Mark send you a message
                    </p>
                    <p className="text-muted mb-0"> 1 Minutes ago </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <img
                      src="assets/images/faces/face2.jpg"
                      alt="per"
                      className="rounded-circle profile-pic"
                    />
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">
                      Cregh send you a message
                    </p>
                    <p className="text-muted mb-0"> 15 Minutes ago </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <img
                      src="assets/images/faces/face3.jpg"
                      alt="per"
                      className="rounded-circle profile-pic"
                    />
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject ellipsis mb-1">
                      Profile picture updated
                    </p>
                    <p className="text-muted mb-0"> 18 Minutes ago </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <p className="p-3 mb-0 text-center">4 new messages</p>
              </div>
            </li>
            <li className="nav-item dropdown border-left">
              <a
                className="nav-link count-indicator dropdown-toggle"
                id="notificationDropdown"
                href="#0"
                data-toggle="dropdown"
              >
                <i className="mdi mdi-bell" />
                <span className="count bg-danger" />
              </a>
              <div
                className="dropdown-menu dropdown-menu-right navbar-dropdown preview-list"
                aria-labelledby="notificationDropdown"
              >
                <h6 className="p-3 mb-0">Notifications</h6>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-calendar text-success" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject mb-1">Event today</p>
                    <p className="text-muted ellipsis mb-0">
                      {" "}
                      Just a reminder that you have an event today{" "}
                    </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-settings text-danger" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject mb-1">Settings</p>
                    <p className="text-muted ellipsis mb-0">
                      {" "}
                      Update dashboard{" "}
                    </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item">
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-link-variant text-warning" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <p className="preview-subject mb-1">Launch Admin</p>
                    <p className="text-muted ellipsis mb-0"> New admin wow! </p>
                  </div>
                </a>
                <div className="dropdown-divider" />
                <p className="p-3 mb-0 text-center">See all notifications</p>
              </div>
            </li>
            <li className="nav-item dropdown">
              <a
                className="nav-link"
                id="profileDropdown"
                href="#0"
                data-toggle="dropdown"
              >
                <div className="navbar-profile">
                  <img
                    className="img-xs rounded-circle"
                    src="assets/images/faces-clipart/pic-4.png"
                    alt=""
                  />
                  <p className="mb-0 d-none d-sm-block navbar-profile-name">
                    {User.email}
                  </p>
                  <i className="mdi mdi-menu-down d-none d-sm-block" />
                </div>
              </a>
              <div
                className="dropdown-menu dropdown-menu-right navbar-dropdown preview-list"
                aria-labelledby="profileDropdown"
              >
                <div className="dropdown-divider" />
                <Link to="/myaccount" className="preview-item-content text-decoration-none">
                  <a className="dropdown-item preview-item">
                    <div className="preview-thumbnail">
                      <div className="preview-icon bg-dark rounded-circle">
                        <i className="mdi mdi-settings text-success" />
                      </div>
                    </div>
                    <div className="preview-item-content">
                      <p className="preview-subject text-white mb-1" style={{ textDecoration: 'none' }}>Ajustes</p>
                    </div>
                  </a>
                </Link>

                <div className="dropdown-divider" />
                <a className="dropdown-item preview-item" onClick={() => handleSignOut()}>
                  <div className="preview-thumbnail">
                    <div className="preview-icon bg-dark rounded-circle">
                      <i className="mdi mdi-logout text-danger" />
                    </div>
                  </div>
                  <div className="preview-item-content">
                    <button className="preview-subject mb-1 text-white" style={{ border: 'none', background: 'none' }} >Cerrar Sesión</button>
                  </div>
                </a>
                <div className="dropdown-divider" />
              </div>
            </li>
          </ul>
          <button
            className="navbar-toggler navbar-toggler-right d-lg-none align-self-center"
            type="button"
            data-toggle="offcanvas"
          >
            <span className="mdi mdi-format-line-spacing" />
          </button>
        </div>
      </nav>
      <Helmet>
        <script src="assets/vendors/js/vendor.bundle.base.js"></script>
        <script src="assets/vendors/chart.js/Chart.min.js"></script>
        <script src="assets/vendors/progressbar.js/progressbar.min.js"></script>
        <script src="assets/vendors/jvectormap/jquery-jvectormap.min.js"></script>
        <script src="assets/vendors/jvectormap/jquery-jvectormap-world-mill-en.js"></script>
        <script src="assets/vendors/owl-carousel-2/owl.carousel.min.js"></script>
        <script src="assets/js/off-canvas.js"></script>
        <script src="assets/js/hoverable-collapse.js"></script>
        <script src="assets/js/misc.js"></script>
        <script src="assets/js/settings.js"></script>
        <script src="assets/js/todolist.js"></script>
        <script src="assets/js/dashboard.js"></script>
      </Helmet>
    </React.Fragment>
  );
}
