import axios from 'axios'

const countCoins = 26;

export async function getCoins () {
    const res = await axios.get("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false")

    const coinsFilter = filterCoins(res.data);

    return {
        coins: res.data,
        coinsFilter: coinsFilter,
        currentPrices: currentPrices(coinsFilter),
    }
}

/* PRIVATE */

export function filterCoinsObject (coins, objfilter) {
    let coinsFilter = [];

    for (let x = 0; x < objfilter.length; x++) {
        const find = objfilter[x];
        for (let y = 0; y < coins.length; y++) {
            const coin = coins[y];
            if (find.name == coin.name) coinsFilter.push(coin);
        }

        if (x == objfilter.length) return;
    }

    return coinsFilter;
}

function currentPrices (coins) {
    let coinsPrices = [];

    for (let i = 0; i < coins.length; i++) {
        const coin = coins[i];
        if (i < countCoins) {
            coinsPrices.push(coin.current_price);
        }
    }

    return coinsPrices;
}


function filterCoins (coins) {
    let coinsFilter = [];
    
    coinsFilter = filterCoinsFirst(coins);

    coinsFilter = coinsFilter.filter(coin => 
            coin.name != "Tether" &&
            coin.name != "USD Coin" &&
            coin.name != "Binance USD" &&
            coin.name != "TerraUSD" &&
            coin.name != "Crypto.com Coin" &&
            coin.name != "Wrapped Bitcoin" &&
            coin.name != "Dai" &&
            coin.name != "Bitcoin Cash"
        )

    return coinsFilter;
}


function filterCoinsFirst (coins) {
    let coinsFilter = [];
    
    // FILTAR LAS 20 PRIMERAS MONEDAS
    for (let i = 0; i < coins.length; i++) {
        const coin = coins[i];
        if (i < countCoins) {
            coinsFilter.push(coin);
        }
    }

    return coinsFilter;
}
