import React, { useEffect, useState } from 'react'
import { Route, Routes } from 'react-router-dom';
import CPanelTablePlansHoldeo from '../../components/cPanel/CPanelTablePlansHoldeo';
import CPanelTableRangeCoins from '../../components/cPanel/CPanelTableRangeCoins';

export default function CPanelHoldeoUser(props) {

    const [user, setUser] = useState({ plans: { holdeo: [] } });

    useEffect(() => {
        const userStorage = JSON.parse(sessionStorage.getItem("userSelected"));
        setUser(userStorage);
    }, [])


    return (
        <div className="container">
            <h4>Datos Generales</h4>
            <div className="border-bottom"></div>

            <div className="row mt-3 border rounded p-3">
                <div className="col">
                    <h5>Nombre:</h5>
                    <span><strong>{user.fullname}</strong></span>
                </div>
                <div className="col">
                    <h5>Documento ID:</h5>
                    <span><strong>{user.ci}</strong></span>
                </div>
                <div className="col">
                    <h5>Email:</h5>
                    <span><strong>{user.email}</strong></span>
                </div>
            </div>

            <div className="border-bottom mt-5"></div>

            <div className="mt-5 mb-5">
                <h4>Acciones</h4>
                <div className="row">
                    <div className="col">
                        <button className='btn btn-warning'><strong>Enviar correo electronico</strong></button>
                    </div>
                    <div className="col">
                        <Routes>
                            <Route path="/plan/:idp" element={<button className='btn btn-primary'>Analizar</button>}  />
                        </Routes>
                    </div>
                </div>
            </div>

            <Routes>
                <Route path="/" element={<CPanelTablePlansHoldeo user={user} />} />
                <Route path="/plan/:idp" element={<CPanelTableRangeCoins />} />
            </Routes>


        </div>
    )
}
