import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Cookies from 'universal-cookie';

import CPanelTableSolicitudsPlan from './CPanelTableSolicitudsPlan'

import { apiURL } from '../../modules/Enums';
import { Route, Routes } from 'react-router-dom';
import CPanelSolicitudsUserPlan from './CPanelSolicitudsUserPlan';

export default function CPanelSolicitudsPlans() {
    const cookies = new Cookies();

    const [Plans, setPlans] = useState([])

    useEffect(() => {

        axios.get(apiURL + `/api/admin/${cookies.get("cpid", {path:"/cpaneñ"})}/2525/solicituds/plans`).then(res => setPlans(res.data));
        
    }, [])
    
    console.log(Plans)

    return (
        <Routes>
            <Route path="/" element={<CPanelTableSolicitudsPlan plans={Plans} />} />
            <Route path="/:id" element={<CPanelSolicitudsUserPlan/>} />
        </Routes>
    )
}
